<template>
    <transition name="modal">
        <div class="modal d-block my-popups  dialog">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <slot name="header">
                            {{$t('AddGroup')}}
                        </slot>
                        <button v-on:click="$emit('close')" type="button" title="" class="close" data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                    </div>
                    <div class="modal-body">
                        <Form  ref="form" v-slot="{valid,errors}">
                            <div class="d-flex flex-wrap">
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group">
                                                <label>{{$t('GroupName')}}<span class="text-danger">*</span></label>
                                                <Field name="group Name" :rules="{'required':true}" v-slot="{errors}">
                                                    <input   v-bind:class="{'form-control' : true,'is-invalid': errors[0]}"  maxlength="100" v-on:keyup="HideMessage()"  v-model="groupName" name="groupName" type="text">
                                                    <span   name="groupName" class="invalid-feedback">{{errors[0]}}</span>
                                                    <span v-if="duplicate==1" class="text-danger">{{$t('Groupnamealreadyexists')}}</span>
                                                </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                            <div class="form-group">
                                                <label>{{$t('Description')}}</label>
                                                <Field name="groupDescription" :rules="{'required':false}" label="groupDescription" v-slot="{errors,touched,invalid}">
                                                <textarea v-model="groupDescription"  v-bind:maxlength="800" class="form-control" rows="5" type="text" name="groupDescription"></textarea>
                                                <span   name="groupDescription" class="invalid-feedback">{{errors[0]}}</span>
                                            </Field>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="row flex-row-reverse mt-2">
                                    <div class="col-lg-6 text-right">
                                        <a href="javascript:;" class="btn btn-success formbtn text-white mr-1" v-on:click="SaveToGroup(valid,errors)"><em  class="fa fa-save pr-2"></em>{{$t('Save')}}</a>
                                            <a v-on:click="$emit('close')" class="btn btn-danger formbtn widthhalf text-white"><em  class="fa fa-close pr-2"></em>{{$t('Cancel')}}</a>
                                    </div>
                                    <div class="col-lg-6">
                                        <div class="py-1">
                                            <small class="text-danger">
                                                {{ $t("MandatoryString") }} </small>
                                        </div>
                                    </div>
                                </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
  import AddGroupDataService from "../../../services/DataService"
export default {
    data:function() {
        return {
            groupName: '',
            groupstatusid: '',
            groupDescription: '',
            companyId: '',
            createdBy: '',
            groupStatus: [],
            duplicate: 0,
        }
    },
    created: function () {
        this.GetStatus();
    },
    methods:{
        HideMessage: function () {
                this.duplicate = 0;
                $("input[name='groupName']").removeClass('is-invalid');
            },
        GetStatus() {
                var vm = this;
                var url = `id=&moduleName=${vm.$route.params.moduleName}&type=GROUP_STATUS`;                 
                AddGroupDataService.GetDDLDataForAddGroup(url).then(function (response) {
                            var json = response.data;                             
                            if (json != null) {
                                vm.groupStatus = json.DATA;
                                return vm.groupStatus;
                            }                             
                 });
        },
        SaveToGroup: function (valid,errors) {
            var vm=this; 
            // this.$refs.form.setFieldTouched('groupName', true);              
            this.$refs.form.validate().then((result) => {          
            var checkName = vm.groupName;
            if (checkName != '') {
                var url = `name=${checkName}&type=GROUPNAME`;
                AddGroupDataService.CheckDuplicate(url).then(function (response) {
               
                    if (response.data != null) {
                        vm.duplicate = response.data;
                        if (vm.duplicate != 1) {
                            if (result) {
                            var data = {
                                name: vm.groupName,
                                description: vm.groupDescription,
                                status_id: 1001
                            };
                            var objectPostString = JSON.stringify(data);
                            AddGroupDataService.SaveContactGroup(objectPostString).then(function (response) {
                                if (response.status == 200) {
                                   
                                    vm.ShowAlert(vm.$t("Contact_Group_Created"), "success", true, vm.$t("Alert"))
                                    
                                }
                                else{
                                    vm.ShowAlert(vm.$t("Something_Wrong"), "failure", true, vm.$t("Alert"))
                                } 
                                vm.$emit('close');
                                vm.$parent.loadData('contactGroups');                               
                            });
                        }
                        }
                        else {
                            $("input[name='groupName']").addClass('is-invalid');
                        }
                              }  
        });                       
    }
});

    }

}
}
</script>