<template>
  
        <div class="modal d-block my-popups dialog">
            <loader :is-visible="isLoading"></loader>
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <slot name="header">
                            <h5 class="modal-title">{{ contactHeader }}</h5>
                        </slot><button type="button" class="close" v-on:click="$emit('close')"
                            data-original-title="Close"><em class="fa fa-times"></em></button>
                    </div>
                    <div class="modal-body" style="max-height: 70vh;overflow: auto;">
                        <div class="row form-head" style="display:none;">
                            <div class="col-12">
                                <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 float-right p-0">
                                    <div class="userguide-box userguide_curve"><a data-toggle="collapse"
                                            href="#user-guidepriority" aria-expanded="false"
                                            aria-controls="search-content" class="userguide-btn collapsed"><em            class="fa"></em><span>{{ $t("GuideForUser") }}
                                            </span></a>
                                        <div class="clearfix"></div>
                                        <div id="user-guidepriority" class="user-guide collapse">Fill the Form to add
                                            contact. Some fields are not mendatory, but please fill these fields for
                                            more information for in future when needed.<br><br></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <Form  v-slot="{valid,errors}" ref="form"> 
                            <div class="d-flex flex-wrap">
                                <div class="col-md-12 p-0">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("FirstName") }} :<span
                                                        class="text-danger">*</span></label>
                                                    <Field name="First Name" :rules="{'required':true}"  v-slot="{errors}">
                                                    <input v-bind:class="{'form-control' : true,'is-invalid': errors[0]}" id="txtFirstName" name="txtFirstName"
                                                    maxlength="100" v-model="contactData.txtFirstName"  type="text">
                                                    <span v-if="errors[0]"  name="First Name" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>   
                                                    </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("LastName") }} :<span
                                                        class="text-danger">*</span></label>
                                                <Field name="Last Name" :rules="{'required':true}"  v-slot="{errors}" >
                                                <input v-bind:class="{'form-control' : true,'is-invalid': errors[0]}" id="txtLastName" name="txtLastName"
                                                maxlength="100" v-model="contactData.txtLastName" type="text">
                                                <span  v-if="errors[0]" name="Last Name" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                </Field>   
                                            </div>
                                        </div>

                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("EmailId") }} :<span
                                                        class="text-danger">*</span></label>
                                                        <Field name="Email" :rules="{'required':true,'email':true,'max':30}"  v-slot="{errors}">
                                                        <input v-bind:class="{'form-control' : true,'is-invalid': errors[0]}" id="txtEmailId" 
                                                        name="txtEmailId" v-model="contactData.txtEmailId" type="text">
                                                        <span v-if="errors[0]"  name="Email" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>  
                                                     </div>
                                                 </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("Mobile") }} :</label>
                                                <Field name="MobilePhone" :rules="{'required':false}"  v-slot="{errors}">
                                                <vue-tel-input id="txtMobilePhone"
                                                    v-model="contactData.txtMobilePhone" name="txtMobilePhone"
                                                    mode="international"  @input="onInputMobilePhone">
                                                </vue-tel-input>
                                                
                                                <span id="spantxtMobilePhone" v-if="mobilephoneError" class="invalid-feedback d-block">Enter valid {{ $t("MOBILE_PHONE") }}.</span>
                                                <span  v-if="errors[0]" name="MobilePhone" class="invalid-feedback d-block" >{{errors[0]}}</span>

    </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("Home") }}
                                                    {{ $t("Phone") }} :</label>
                                                    <Field name="txtHomePhone" :rules="{'required':false}"  v-slot="{errors}">
                                                <vue-tel-input id="txtHomePhone" 
                                                    v-model="contactData.txtHomePhone" name="txtHomePhone"
                                                    mode="international" @input="onInputHomePhone">
                                                </vue-tel-input>
                                                <span  v-if="errors[0]" name="txtHomePhone" class="invalid-feedback d-block" >{{errors[0]}}</span>
                                                <span id="spantxtHomePhone" v-if="homephoneError" class="invalid-feedback d-block">Enter valid {{$t("Home")}} {{$t("Phone")}}.</span>
    </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("BusinessPhone") }}
                                                    :</label>
                                                    <Field name="txtBusinessPhone" :rules="{'required':false}"  v-slot="{errors}">
                                                <vue-tel-input id="txtBusinessPhone"  @input="onInputBusinessPhone"
                                                    v-model="contactData.txtBusinessPhone" name="txtBusinessPhone"
                                                    mode="international">
                                                </vue-tel-input>
                                                <span  v-if="errors[0]" name="txtBusinessPhone" class="invalid-feedback d-block" >{{errors[0]}}</span>
                                                <span id="spantxtBusinessPhone" v-if="businessphoneError==true" class="invalid-feedback d-block">Enter valid {{ $t("BusinessPhone") }}.</span>
    </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("Fax") }}:</label>  
                                                    <Field name="txtFax" :rules="{'required':false}"  v-slot="{errors}" >
                                                    <input class="form-control" id="txtFax" name="Fax"
                                                    v-on:keypress="isNumber($event)" maxlength="15"
                                                    v-model="contactData.txtFax" type="text">
                                                    <span v-if="errors[0]"  name="txtFax" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field> 
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group">
                                                <label>{{$t("AddressStreet1")}}:</label>
                                                <Field name="txtAddressLine1" :rules="{'required':false}"  v-slot="{errors}" >
                                                <input class="form-control" id="txtAddressLine1" name="txtAddressLine1" maxlength="200" 
                                                v-model.trim="contactData.txtAddressLine1" type="text">
                                                <span v-if="errors[0]"  name="txtAddressLine1" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                </Field>
                                                </div>
                                                </div>       
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("AddressStreet2") }}:</label>
                                                    <Field name="txtAddressLine2" :rules="{'required':false}"  v-slot="{errors}" >
                                                    <input class="form-control" id="txtAddressLine2"
                                                    name="txtAddressLine2" maxlength="200"
                                                    v-model="contactData.txtAddressLine2" type="text">
                                                    <span   name="txtAddressLine2" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>  </div>
                                        </div>

                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("City") }}:</label>
                                                    <Field name="txtCity" :rules="{'required':false}"  v-slot="{errors}" >
                                                    <input class="form-control" id="txtCity" name="txtCity" 
                                                    maxlength="100" v-model.trim="contactData.txtCity" type="text" />
                                                    <span   name="txtCity" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("State") }}
                                                    :</label>
                                                    <Field  name="State"  :rules="{ 'required': false }" v-slot="{ errors}" >
                    <select class="form-control" v-model="contactData.txtState">
                        <option value="">Select</option>
                        <option v-for="(item,index) in stateDDLData" v-bind:value="item.value" :key="index">{{ item.name }}</option>
                    </select>
                    <span v-if="errors[0]"  name="State" class="invalid-feedback d-block">{{errors[0]}}</span>             
                                                            </Field>
                                             
                                                </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("ZipCode") }}:</label>
                                                    <Field name="ZipCode" :rules="{'ZipCode':true}"  v-slot="{errors}" >
                                                    <input class="form-control"
                                                    v-on:keypress="isNumber($event)" id="txtZipCode" name="ZipCode"
                                                    maxlength="15" v-model="contactData.txtZipCode" type="text">
                                                    <span  v-if="errors[0]" name="ZipCode" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                            <div class="form-group"><label>{{ $t("Country") }}
                                                    :</label>                                                    
                                                    <Field name="Country" :rules="{'required':false}"  v-slot="{errors}">                                                  
                                                    <select class="form-control" v-model="contactData.country" v-on:change="FillDropdown(contactData.country)">
                                                        <option value="">Select</option>
                                                    <option v-for="(item,index1) in countryDDLData" v-bind:value="item.value"  :key="index1">
                                                        {{ item.name }}</option>
                    </select>
                    <span  v-if="errors[0]" name="Country" class="invalid-feedback d-block">{{errors[0]}}</span>    
                                                    </Field>

                                                </div>
                                        </div>
<div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                        <label class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">Gender:</label>
                                        <div class="form-group float-left">
                                            <label class="switch medium">
                                                <Field name="Gender" :rules="{'required':false}"  v-slot="{errors}" >
                                                <input class="form-check-input custom-control-input dynamic"
                                                    type="checkbox" :value="contactData.rGender"
                                                    v-on:change="onChangeToggle($event)"  v-bind:checked="contactData.rGender">
                                                <span class="slider round">
                                                    <span class="slider-yes">Male</span>
                                                    <span class="slider-no">Female</span>
                                                </span>
                                                <span v-if="errors[0]"  name="Gender" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                 </Field>
                                            </label>
                                        </div>
                                    </div>

                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                        <div class="form-group"><label>{{ $t("DateOfBirth") }}
                                                :</label>
                                            <div class="input-group">                                              
                                                    <v-date-picker :minute-increment="5"   class="input-group" v-model="contactData.txtDateOfBirth"
                                                         :max-date='new Date()'>
                                                        <template v-slot="{ inputValue, inputEvents,togglePopover }">
                                                            <input class="form-control" :value="inputValue" v-on="inputEvents" />
                                                            <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em></span></div> 
                                                        </template>
                                                    </v-date-picker>  
                                                                                                                                              
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                        <div class="form-group"><label>
                                                {{ $t("MaritalStatus") }}
                                                :</label>
                                                <Field id="MaritalStatus" name="MaritalStatus" :rules="{ 'required': false }" v-slot="{errors}" >
     <select class="form-control" v-model="contactData.maritalStatus" v-on:change="ChangeMarital()">
                                                   <option value="">Select</option>
                                                            <option value="Single">Single</option>
                                                            <option value="Married">Married</option>
                                                            <option value="Divorced">Divorced</option>

                            </select>                    
    <span   name="MaritalStatus" class="invalid-feedback d-block">{{ errors[0] }}</span>                          
                                                </Field>
                                            </div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-right">
                                        <div class="form-group"><label>
                                                {{ $t("SpouseName") }}
                                                :</label>
                                                <Field name="txtSpouseName" :rules="{'required':false}"  v-slot="{errors}" >
                                                <input class="form-control" id="txtSpouseName"
                                                name="txtSpouseName" maxlength="100" 
                                                v-model="contactData.txtSpouseName" type="text">
                                                <span   name="txtSpouseName" class="invalid-feedback d-block">{{errors[0]}}</span>
                                                    </Field>
                                                </div>
                                    </div>
                                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                                        <div class="form-group"><label>
                                                {{ $t("Status") }}
                                                :<span class="text-danger">*</span></label>
                                                <Field :rules="{ 'required': true }"  id="Status" name="Status"  v-slot="{ errors }">
                <select class="form-control"  v-model="contactData.Status">
                    <option value="">Select</option>
                    <option v-for="(item,index2) in statusDDLData" v-bind:value="item.value" :key="index2">{{ item.name }}</option>
                </select>
                <span   name="Status" class="invalid-feedback d-block">{{ errors[0] }}</span>                     
                                                    </Field>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left"
                                        v-if="submodulecode == 'CRM_CAMPAIGNS'">
                                        <div class="form-group"><label>{{ $t("GroupName") }}
                                                :</label>
                                                <Field  :rules="{'required':false}"   id="GroupName" name="GroupName"  v-slot="{ errors}">
        <select class="form-control" v-model="contactData.txtGroupName">
            <option value="">Select Group Name</option>
                                                <option v-for="(item,index3) in groupDDLData" v-bind:value="item.value" :key="index3">
                                                    {{ item.name }}</option>

                    </select>
                    <span   name="GroupName" class="invalid-feedback d-block">{{errors[0]}}</span>                         
                                                </Field>
                                    </div></div>
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                        <div class="form-group"><label>{{ $t("Description") }}
                                                :</label> <Field name="txtPersonalNotes" :rules="{ 'required': false }" 
                                                    v-slot="{ errors }"><textarea class="form-control" id="txtPersonalNotes"
                                                name="txtPersonalNotes" maxlength="1000" 
                                                style="max-height:100px!important;min-height:100px!important"
                                                v-model="contactData.txtPersonalNotes"></textarea>
                                                <span   name="txtPersonalNotes"
                                                        class="invalid-feedback">{{ errors[0] }}</span>
                                                    </Field></div>
                                    </div>

                                
                                   
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                <div class="modal-footer text-right">
                                        <a href="javascript:;" class="btn btn-success mr-1"
                                            v-on:click="SaveContact(valid,errors)"><em class="fa fa-save pr-2"></em>
                                            {{ $t("submit") }} </a>
                                        <a href="javascript:;" v-on:click="CloseAddContactPopup"
                                            class="btn btn-danger delete-btn"><em class="fa fa-times"></em>
                                            {{ $t("Cancel") }}</a>

                                    </div>  
                                    <div class= "my-3 float-left text-danger">
                                        {{ $t("MandatoryString") }}
                                    </div>
                                </div>
</div>
                            </div>
                            </div> 
                        </Form>
                    </div>
                </div>

            </div>
        </div>
   
</template>

<script>
import LeadDataService from "../../../services/DataService";
export default {
    props: {
        sourceid: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        userinfo: {
            type: Number,
            required: true
        },
        companyid: {
            type: Number,
            required: true
        },
        modulename: {
            type: String,
            required: true
        },
        sourcename: {
            type: String,
            required: true
        },
        iscontactDetails: null
    },
    data: function () {
        return {
            options: {
                format: null,
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                isLoading: false,

            },
            popover: {
                visibility: 'click'
            },
            datePickerMaxConfig: {
                format: null,
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                maxDate: new Date(Date.now())
            },
            countryDDLData: [],
            stateDDLData: [],
            contactHeader: '',
            maxLength: 20,
            statusDDLData: [],
            groupDDLData: [],
            contactData: {
                contactId: null,
                txtFirstName: null,
                txtLastName: null,
                txtJobTitle: null,
                txtEmailId: null,
                txtBusinessPhone: null,
                txtMobilePhone: null,
                maritalStatus: '',
                txtGroupName: '',
                txtFax: null,
                txtPreferredContact: null,
                txtAddressLine1: null,
                txtAddressLine2: null,
                txtAddressStreet3: null,
                txtCity: null,
                txtState: '',
                txtZipCode: null,
                txtHomePhone: null,
                country: '',
                rGender: null,
                txtSpouseName: null,
                txtDateOfBirth: null,
                txtPersonalNotes: null,
                Status: '',
                type: '',
                userId: null,
                companyId: null,
                sourceId: null,
                moduleName: null,
                subModuleCode: null,
                groupID: null                
            },
            phoneList: [],
            businessphoneError: false,
            homephoneError: false,
            mobilephoneError: false
              

        }
    },
    created: function () {
        var vm = this;
        if (vm.iscontactDetails != null && vm.iscontactDetails != '') {
            vm.contactHeader = vm.$t('EditContact');
        } else {
            vm.contactHeader = vm.$t('AddContact');
        }
        setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
        vm.GetCountryDDLValues();
        vm.GetStatusDDLData();
        vm.fetchContactForForm();
     vm.GetGroupNameDDLData();
        setTimeout(function () {
            vm.ChangeMarital();
            // if (vm.contactData.contactId == null || $.trim(vm.contactData.txtBusinessPhone) == "" || $.trim(vm.contactData.txtHomePhone) == "" || $.trim(vm.contactData.txtMobilePhone) == "") {
            //     var Phones = [];
            //     if ($.trim(vm.contactData.txtBusinessPhone) == "")
            //         Phones.push("txtBusinessPhone");
            //     if ($.trim(vm.contactData.txtHomePhone) == "")
            //         Phones.push("txtHomePhone");
            //     if ($.trim(vm.contactData.txtMobilePhone) == "")
            //         Phones.push("txtMobilePhone");
            //     $.each(Phones, function (ind, phn) {
            //         $.each(vm.$children, function (indx, elements) {
            //             if (elements.$children.length > 0) {
            //                 $.each(elements.$children, function (index, element) {
            //                     if (element.$el._prevClass.indexOf("vue3-tel-input") > -1 && element.$el.id == phn)
            //                         element.countryCode = 'US';
            //                 });
            //             }
            //         });
            //     });

            //     $("#txtFirstName").focus();
                
            // }
        }, 500);
        setTimeout(function () {
            vm.FillDropdown(vm.contactData.country, 'EDIT');
        }, 1000)
    },
    beforeUpdate: function () {
        var vm = this;
       vm.ChangeMarital(vm.contactData.maritalStatus);
    },
    methods: {
        onChangeToggle(event){
            this.contactData.rGender=event.target.checked;
        },          
        ChangeMarital: function () {
            var vm=this;
            var MaritalStats = vm.contactData.maritalStatus;//$('#MaritalStatus').val();
            if (MaritalStats == "Single" || MaritalStats == "") {
                $("#txtSpouseName").attr("disabled", "disabled");
            }
            else {
                $("#txtSpouseName").removeAttr("disabled");
            }
        },
        onInputMobilePhone(phone, phoneObject, input) {
            if(phone){
            if(phoneObject && phoneObject?.formatted.length >0 && (!phoneObject?.valid || phoneObject?.valid==undefined)){
                this.mobilephoneError = true;
            } else
                if (phoneObject?.formatted && phoneObject?.valid) {
                    this.contactData.txtMobilePhone = phoneObject.formatted;
                    this.mobilephoneError = false;
                }
                var PhnData = {
                    field_id: "txtMobilePhone",
                    value: $.trim(phoneObject.formatted),
                    isValid: this.mobilephoneError==false
                }
                var newdata = this.phoneList.filter(v => v.field_id != "txtMobilePhone");
                newdata.push(PhnData);
                this.phoneList = newdata;
        
            }
        },
        onInputHomePhone(phone, phoneObject, input) {
            if(phone){
            if(phoneObject && phoneObject?.formatted.length >0 && (!phoneObject?.valid || phoneObject?.valid==undefined)){
                this.homephoneError = true;
            } else
            if (phoneObject?.formatted && phoneObject?.valid) {
                this.contactData.txtHomePhone = phoneObject.formatted;
                this.homephoneError = false;
            }
            var PhnData = {
                    field_id: "txtHomePhone",
                    value: $.trim(phoneObject.formatted),
                    isValid: this.homephoneError==false
                }
                var newdata = this.phoneList.filter(v => v.field_id != "txtHomePhone");
                newdata.push(PhnData);
                this.phoneList = newdata;
        
            }
        },
        onInputBusinessPhone(phone, phoneObject, input) {
            if(phone){
            if(phoneObject && phoneObject?.formatted.length >0 && (!phoneObject?.valid || phoneObject?.valid==undefined)){
             this.businessphoneError = true;
            } else
            if (phoneObject?.formatted && phoneObject?.valid) {
                this.contactData.txtBusinessPhone = phoneObject.formatted;
                this.businessphoneError = false;
            }
            var PhnData = {
                    field_id: "txtBusinessPhone",
                    value: $.trim(phoneObject.formatted),
                    isValid: this.businessphoneError==false
                }
                var newdata = this.phoneList.filter(v => v.field_id != "txtBusinessPhone");
                newdata.push(PhnData);
                this.phoneList = newdata;
        }
        },  
        SaveContact: function (valid,errors) {
        //     this.$refs.form.setFieldTouched('txtFirstName', true);  
        //   this.$refs.form.setFieldTouched('txtLastName', true); 
        //    this.$refs.form.setFieldTouched('txtEmailId', true); 
        //    this.$refs.form.setFieldTouched('Status', true); 
           
            var vm = this;
            var checkValue = null;
            checkValue = vm.iscontactDetails;
            vm.$refs.form.validate().then((result) => {
            if (result) {
                if(vm.phoneList && vm.phoneList.length>0 && vm.phoneList.some(x=>x.isValid==false)){
                 return false;
                }
                vm.maritalStatus = ((vm.maritalStatus != '') ? vm.maritalStatus : null);
                vm.country = ((vm.country != '') ? vm.country : null);
                vm.contactData.userId = vm.userinfo;
                vm.contactData.companyId = vm.companyid;
                vm.contactData.sourceId = vm.sourceid;
                vm.contactData.moduleName = vm.modulename;
                vm.contactData.subModuleCode = vm.submodulecode;
                vm.contactData.groupID = vm.txtGroupName;
                vm.contactData.type = vm.sourcename;               
                
                vm.contactData.rGender = vm.contactData.rGender ? "Male" : "Female";
                var objectPostString = JSON.stringify(vm.contactData);
                vm.contactData.rGender = vm.contactData.rGender=="Male"?true:false;
                LeadDataService.SaveContactWithMapping(objectPostString).then(function (response) {
                 
                    if (response.data[0].Status == "Success") {
                        vm.$emit('close');
                        vm.$parent.loadData('contacts');
                        if (checkValue != null) {
                            vm.ShowAlert(vm.$t('UpdatedSuccess',[vm.$t('Contact')]), "success", true, vm.$t("Alert"));
                        }
                        else {
                            vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Contact')]), "success", true, vm.$t("Alert"));
                            vm.$parent.openCRMExistingContactModel();
                        }
                    }
                    else if (response.data[0].Status.toUpperCase() == "EXIST") {
                        vm.ShowAlert(vm.$t("Email id already exist."), "warning", true, vm.$t("Alert"));
                    }
                     
                });
            }

         });
        },       
        isNumber: function (evt) {
        var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((evt.target.id == 'txtFax' || evt.target.id == 'txtZipCode') && charCode == 46) {
                evt.preventDefault();
            }
            else if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode != 46) {
                evt.preventDefault();
            } else {
                return true;
            }
        },
        fetchContactForForm: function () {
            var vm = this;
            this.isLoading = true;
            var checkValue = vm.iscontactDetails;
            if (checkValue != null && checkValue != 0) {
                var url = "id=" + checkValue;
                LeadDataService.GetSingleContactDetails(url).then(function (response) {                    
                    if (response.data != null) {
                        vm.contactData.contactId = response.data.CONTACT_ID;
                        vm.contactData.txtFirstName = response.data.FIRST_NAME;
                        vm.contactData.txtLastName = response.data.LAST_NAME;
                        vm.contactData.txtJobTitle = response.data.JOB_TITLE;
                        vm.contactData.txtEmailId = response.data.EMAIL_ID;
                        vm.contactData.txtBusinessPhone = response.data.BUSINESS_PHONE?response.data.BUSINESS_PHONE:'';
                        vm.contactData.txtHomePhone = response.data.HOME_PHONE?response.data.HOME_PHONE:'';
                        vm.contactData.txtMobilePhone = response.data.MOBILE_PHONE?response.data.MOBILE_PHONE:'';
                        vm.contactData.maritalStatus = ((response.data.MARITAL_STATUS != null) ? response.data.MARITAL_STATUS : '');
                        vm.contactData.txtFax = response.data.FAX;
                        vm.contactData.txtPreferredContact = response.data.PREFERRED_CONTACT;
                        vm.contactData.txtAddressLine1 = response.data.ADDRESS_STREET_1;
                        vm.contactData.txtAddressLine2 = response.data.ADDRESS_STREET_2;
                        vm.contactData.txtAddressStreet3 = response.data.ADDRESS_STREET_3;
                        vm.contactData.txtCity = response.data.CITY;
                        vm.contactData.txtState = response.data.STATE;
                        vm.contactData.txtZipCode = response.data.ZIPCODE;
                        vm.contactData.country = ((response.data.COUNTRY_ID == null || response.data.COUNTRY_ID == 0) ? '' : response.data.COUNTRY_ID);
                        if (response.data.GENDER == "false")
                            vm.contactData.rGender = false;
                        else
                            vm.contactData.rGender = response.data.GENDER == "Male" ? true : false;
                        vm.contactData.txtSpouseName = response.data.SPOUSE_NAME;
                        vm.contactData.txtDateOfBirth =response.data.DATE_OF_BIRTH?vm.$options.filters.formatDate(response.data.DATE_OF_BIRTH):'';
                        vm.contactData.txtPersonalNotes = response.data.PERSONAL_NOTES;
                        vm.contactData.Status =String(response.data.STATUS_ID);
                        vm.contactData.type = vm.sourcename;
                        vm.contactData.txtGroupName = ((response.data.GROUP_ID != null) ? response.data.GROUP_ID : '');
                     var frmFields={}
                     frmFields['contactId']=vm.contactData.contactId;
                     frmFields['txtFirstName']=vm.contactData.txtFirstName;
                     frmFields['txtLastName']=vm.contactData.txtLastName;
                     frmFields['txtJobTitle']=vm.contactData.txtJobTitle;
                     frmFields['txtEmailId']=vm.contactData.txtEmailId;
                     frmFields['txtBusinessPhone']=vm.contactData.txtBusinessPhone;
                     frmFields['txtHomePhone']=vm.contactData.txtHomePhone;
                     frmFields['txtMobilePhone']=vm.contactData.txtMobilePhone;
                     frmFields['maritalStatus']=vm.contactData.maritalStatus;
                     frmFields['txtFax']=vm.contactData.txtFax;
                     frmFields['txtPreferredContact']=vm.contactData.txtPreferredContact;
                      frmFields['txtAddressLine1']=vm.contactData.txtAddressLine1;
                       frmFields['txtAddressLine2']=vm.contactData.txtAddressLine2;
                        frmFields['txtAddressStreet3']=vm.contactData.txtAddressStreet3;
                        frmFields['txtCity']=vm.contactData.txtCity;
                        frmFields['txtState']=vm.contactData.txtState;
                         frmFields['txtZipCode']=vm.contactData.txtZipCode;
                         frmFields['country']=vm.contactData.country;
                         frmFields['rGender']=vm.contactData.rGender;
                         frmFields['txtSpouseName']=vm.contactData.txtSpouseName;
                         frmFields['txtDateOfBirth']=vm.contactData.txtDateOfBirth;
                         frmFields['txtPersonalNotes']=vm.contactData.txtPersonalNotes;
                         frmFields['Status']=vm.contactData.Status;
                         frmFields['type']=vm.contactData.type;
                         frmFields['txtGroupName']=vm.contactData.txtGroupName;                         
                    // vm.$refs.form.resetForm({values:frmFields});
                    }
                    vm.ChangeMarital();
                });
            }
            this.isLoading = false;
        },
        GetCountryDDLValues: function () {
            var vm = this;
            var url = "id=&moduleName=" + vm.modulename + "&type=COUNTRIES";
             
            LeadDataService.getDDLData(url)
                .then(function (response) {
                     
                    if (response.data != null) {
                        vm.countryDDLData = response.data.DATA;
                    }
                    var cntrydata = vm.countryDDLData;
                    if (vm.contactData.contactId == 0 || vm.contactData.contactId == null) {
                        $.each(cntrydata, function (index, item) {
                            if (item.name == "United States")
                                vm.contactData.country = item.value;
                        });
                        //vm.FillDropdown(vm.contactData.country);
                    }
                     
                });
        },
        FillDropdown: function (value, type) {
            var vm = this;
            var refId = value;
            var typeCode = 'ACCOUNT_STATE';
            var url = "id=&moduleName=CRM&type=" + typeCode + "&refId=" + refId;
            LeadDataService.getDDLData(url).then(function (response) {
                var json = [];
                if (response.data != null) {
                    json = response.data.DATA;
                    vm.stateDDLData = json;
                    if (type != 'EDIT') {
                        vm.contactData.txtState = '';
                    }
                }
            });
        },        
        GetStatusDDLData: function () {
           
            var vm = this;
            var url = "id=&moduleName=" + vm.modulename + "&type=GLOBAL_STATUS";
             
            LeadDataService.getLeaOwnerBySearch(url)
                .then(function (response) {
                    if (response.data != null) {
                        vm.statusDDLData = response.data.DATA;
                    }
                     
                });
        },
        GetGroupNameDDLData: function () {
            var vm = this;
            var url = "id=&moduleName=" + vm.modulename + "&type=CRM_GROUP_NAME";
             
            LeadDataService.getDDLData(url)
                .then(function (response) {
                     
                    if (response.data != null) {
                        vm.groupDDLData = response.data.DATA;
                    }
                     
                });
        },
        CloseAddContactPopup: function () {
            var vm = this;
            var checkValue = null;
            checkValue = vm.iscontactDetails;
            vm.$emit("close");
            if (checkValue == null)
                vm.$parent.openCRMExistingContactModel();
        },
    },
}
</script>