<script>
import VueApexCharts from "vue-apexcharts";
import CampaignDataServices from "../../../services/DataService";

export default {
    components: {
        VueApexCharts
    },
    props: {
        isRightContent: {
            type: Boolean
        }
    },
    data: function () {
        return {
            startEndDate: {
                start: new Date(),
                end: new Date(),
            },
            series:[],
            chartOptions: {}
        }
    },
    methods: {
        onDateChange() {
            var vm = this;
            vm.dateform = vm.startEndDate.start;
            vm.dateto = vm.startEndDate.end;
            var url = `campaign_id=0&dateform=${vm.$options.filters.formatDate(vm.dateform)}&dateto=${vm.$options.filters.formatDate(vm.dateto)}`;
            CampaignDataServices.GetCampaignStatistics(url).then(response => {
                vm.series = [];
                vm.chartOptions = [];
                var getDates = vm.GetDateDifference();

                // vm.series[0].data.push(response.data.DELIVERED_COUNT);
                // vm.series[1].data.push(response.data.READ_COUNT);
                // vm.series[2].data.push(response.data.CLICKED_COUNT);
                // vm.series[3].data.push(response.data.SOFT_BOUNCED_COUNT);
                // vm.series[4].data.push(response.data.HARD_BOUNCED_COUNT);
                // vm.series[5].data.push(response.data.SPAM_COUNT);
                // vm.series[6].data.push(response.data.UNSUBSCRIBED_COUNT);

                vm.series = [{
                    name: 'DELIVERED COUNT',
                    data: []
                },
                {
                    name: 'READ COUNT',
                    data: []
                },
                {
                    name: 'CLICKED COUNT',
                    data: []
                },
                {
                    name: 'SOFT BOUNCED COUNT',
                    data: []
                },
                {
                    name: 'HARD BOUNCED COUNT',
                    data: []
                },
                {
                    name: 'SPAM COUNT',
                    data: []
                },
                {
                    name: 'UNSUBSCRIBED COUNT',
                    data: []
                }]
                vm.series[0].data.push(5,6,7,8,9,11,12);
                vm.series[1].data.push(10,13,14,16,17,18);
                vm.series[2].data.push(15,19,21,22,23,24,26);
                vm.series[3].data.push(20,27,28,29,31,32,33);
                vm.series[4].data.push(25,34,36,37,38,39,41);
                vm.series[5].data.push(30,42,43,44,46,47,48);
                vm.series[6].data.push(35,49,51,52,53,54,56);
                vm.chartOptions = {
                    toolbar: 
                    {
                        show: false
                    },
                    xaxis: {
                        categories: getDates,
                        title: {
                            text: 'Month'
                        }
                    },
                    yaxis: {
                        title: {
                            text: 'Temperature'
                        },
                    },
                    dataLabels: {
                        enabled: true,
                    },
                    stroke: {
                        curve: 'smooth'
                    },
                }
            })
        },
        GetDateDifference(){
            var vm = this;
            const date = new Date(vm.dateform.getTime());
            const dates = [];
            const getFinalDates = [];
            while (date <= vm.dateto) {
                dates.push(new Date(date));
                date.setDate(date.getDate() + 1);
            }
            dates.forEach(item=>{
                getFinalDates.push(vm.$options.filters.formatDate(item));
            })
            return getFinalDates;
        }
    }
}
</script>