                                    <a href="javascript:;" class="mr-3" v-if="ListView" v-on:click="ChangeView('List')"
                                       ><em  class="fa fa-th pr-2"></em>{{$t('Card View')}}</a>
                                    <a href="javascript:;" v-if="GridView" v-on:click="ChangeView('Grid')"
                                        class="mr-3"><em  class="fa fa-th-list pr-2"></em>{{$t('List View')}}</a>
                                    <a href="javascript:;" v-on:click="FetchData()"><em 
                                            class="fa fa-refresh pr-2"></em>{{$t('Refresh')}}</a>
                                </small>
                            </h5>
                        </div>
