                                                    v-bind:value="item.TEMPLATE_ID"
                                                    v-on:change="RadioChange(item,index)" name="template" /></td>
                                            <td class="statuscolor"><span>{{item.TEMPLATE_NAME}}</span><br /></td>
                                            <td class="text-center"><a
