                                                        class="fa fa-pencil action-icon text-success"></em></a></td>
                                        </tr>
                                        </template> 
                                        <tr v-if="EmailTemplateData == null || EmailTemplateData.length == 0"><td colspan="10" class="text-danger text-center">No record found</td></tr>                                                           
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>

                    <div class="tableborder p-3 card-view campaign-template-card-view" v-if="GridView">
                        <div class="row">
                            <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-3 cards-minwidth25"
