                                    </div>
                                    <div class="cardv-body w-100 float-left p-0 position-relative">
                                        <div class="card-text px-2" v-on:click="SelectedDiv(item,index)">
                                            <a><label v-bind:class="{'py-1  w-100  font-16':IsSelectGridDiv==index}">{{item.TEMPLATE_NAME}}</label></a>
                                        </div>

                                    </div>
                                </div>
                            </div>
                            <div v-if="EmailTemplateData == null || EmailTemplateData.length == 0"
                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 cards-minwidth25 text-danger text-center">
                                No record found</div>
                        </div>

                    </div>
                    <div class="modal-footer text-right border-0">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
