                            <a href="javascript:;" v-on:click="SaveSchedulerTime(values,meta)" v-if="SchedulerListForm"
                                class="btn btn-success formbtn"><em class="fa fa-save pr-1"></em>{{$t('Save')}} </a>
                        </div>
                </div>
                    </Form>
                    <Form v-if="ContactListForm" ref="ContactList">
                        <div class="col-sm-12 col-md-12 col-lg-12 p-0">
                            <div class="collapse show pt-2">
                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group">
                                        <label>{{$t('Subject:')}}'<span class="text-danger">*</span></label>
                                        <Field name="Subject" :rules="{'required':true}" label="Subject"
                                            v-slot="{errors,touched,invalid}">
                                            <input v-bind:class="{'form-control':true, 'is-invalid': touched && invalid}" name="Subject"
                                                v-model="Subject" data-vv-as="Subject" type="text"
                                                placeholder="Enter Subject">
                                            <span   name="Subject" class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group">
                                        <label>{{$t('Reply To:')}}'<span class="text-danger">*</span></label>
                                        <Field name="Reply To" :rules="{'required':true}" label="Reply To"
                                            v-slot="{errors,touched,invalid}">
                                            <input v-bind:class="{'form-control':true,'is-invalid': touched && invalid}" name="ReplyTo"
                                                v-model="ReplyTo" data-vv-as="Reply To" type="text" placeholder="Reply to">
                                            <span    name="Reply To" class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left">
                                    <div class="form-group">
                                        <label>{{$t('Sender:')}}'<span class="text-danger">*</span></label>
                                        <Field name="Sender" :rules="{'required':true}" label="Sender"
                                            v-slot="{errors,touched,invalid}">
                                            <input v-bind:class="{'form-control':true,'is-invalid': touched && invalid}" name="Sender"
                                                v-model="Sender" data-vv-as="Sender" type="text" placeholder="Sender">
                                            <span    name="Sender" class="invalid-feedback d-block">{{errors[0]}}</span>
                                        </Field>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left"
                            v-if="ContactListCheckBoxes.length>0">
                            <div class="theme-primary partition">
                                <div class="p-name text-white textelipsis">Selected Contact(s):
                                    <div class="help-labels help-labels-right">
                                        <span class="mr-2"><span class="alert-warning"></span>List</span>
                                        <span><span class="alert-primary"></span>Group</span>
                                    </div>
                                </div>
                            </div>
                            <ul class="float-left pl-0 ulClass minht_contacts pt-2">
