                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                                            <div class="listing">
                                                <div class="table-responsive table-fix-header"
                                                    style="max-height: 255px;overflow-y: auto;">
                                                    <table class="table-sorting table table-bordered"
                                                        id="Schedule_contactList" aria-describedby="scheduleContact">
                                                        <thead class="thead-bg">
                                                            <tr>
                                                                 <th scope="col" style="width:50px !important;" class="text-center">
                                                                    <input id="chkAll"
                                                                        v-on:change="ListAllCheckBox($event,'List')"
                                                                        class="chkitems" value="" type="checkbox"></th>
