                                                                <input v-on:change="ListAllCheckBox($event,'Group')" id="chkAll" value="" class="chkitems" 
                                                                type="checkbox"></th>
                                                                 <th scope="col">Group Name</th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            <tr v-for="(item,index) in contactsGroupData"
                                                            :key="index">
                                                            <td class="text-center">
                                                                <input class="groupAll chkItems" v-on:change="ListCheckBox(item,$event,'Group')" 
                                                                v-bind:data-name="item.NAME" v-bind:value="item.GROUP_ID" type="checkbox">
                                                            </td>
                                                            <td>{{item.NAME}}</td>
                                                        </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                                <div v-if="!contactsGroupData" class="text-center text-danger py-3 mt-3 border">{{$t("NoRecordfound")}}</div>
                                               
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer text-right border-0">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                            <a href="javascript:;" title=Close v-on:click="$emit('close')" class="btn btn-danger mr-2"><em class="fa fa-close pr-1"></em>{{$t('Close')}} </a>
                            <a href="javascript:;" v-on:click="Next(values,meta)" v-if="!SchedulerListForm"
                                class="btn btn-primary formbtn"><span>{{$t('Next')}}</span><em class="fa fa-angle-double-right pl-2"></em></a>
                            <a href="javascript:;" v-on:click="SaveSchedulerTime(values,meta)" v-if="SchedulerListForm"
                                class="btn btn-success formbtn"><em class="fa fa-save pr-1"></em>{{$t('Save')}} </a>
                        </div>
                        </div>
                    </Form>
                    <Form ref="DetailList" v-if="DetailListForm">
                <div class="bordered-content py-3 ticket-overview">
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left">
                                <div class="form-group">
                                    <label>Subject: <span class="detailtxt">{{SchedulerArrayList.Subject}}</span></label>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left">
                                <div class="form-group">
                                    <label>Sender Address: <span class="detailtxt">{{SchedulerArrayList.Sender}}</span></label>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 float-left">
                                <div class="form-group">
                                    <label>Reply-to Address: <span class="detailtxt">{{SchedulerArrayList.ReplyTo}}</span></label>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div class="form-group">
                                    <label>Tracking Code:<span class="text-danger">*</span></label>
                                    <Field name="TrackingCode" :rules="{'required':true}" label="Tracking Code"
                                            v-slot="{errors,touched,invalid}">
                                    <textarea v-bind:class="{'form-control':true, 'is-invalid':touched && invalid}" 
                                    data-vv-as="Tracking Code" name="TrackingCode"
                                        v-model="SchedulerArrayList.TrackingCode"></textarea>
                                        <span name="TrackingCode"  
                                                class="invalid-feedback d-block">{{errors[0]}}</span>
                                    </Field>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer text-right border-0">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                            <a href="javascript:;" title=Close v-on:click="$emit('close')" class="btn btn-danger mr-2"><em class="fa fa-close pr-1"></em>{{$t('Close')}} </a>
                            <a href="javascript:;" v-on:click="Next(values,meta)" v-if="!SchedulerListForm"
                                class="btn btn-primary formbtn"><span>{{$t('Next')}}</span><em class="fa fa-angle-double-right pl-2"></em></a>
                            <a href="javascript:;" v-on:click="SaveSchedulerTime(values,meta)" v-if="SchedulerListForm"
                                class="btn btn-success formbtn"><em class="fa fa-save pr-1"></em>{{$t('Save')}} </a>
                        </div>
                        </div>
                    </Form>
                    <Form ref="SchedulerList" v-if="SchedulerListForm">
                    
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-3">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left m-2 px-0">
                            <div class="passpolicy-detailshead py-3">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-0 mb-xl-0 float-left px-0" style="font-size: 16px;">
                                        When would you like to send this campaign?<span></span></div>
                                </div>
                                <div class="passpolicy-details pt-2">
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                        <div class="form-group">
                                            <div class="form-control bg-transparent pl-0 border-0">
                                                <div class="form-check form-check-inline">
                                                    <div class="form-check form-check-inline custom-control mx-2 p-0 custom-radio">
                                                        <input data-change-id="no" class="form-check-input check-PersonalInfoAllowed custom-control-input" 
                                                        v-on:change="SendType('Now')" name="rd5" id="EnablePersonalInfoAllowed" checked="checked" type="radio"><label class="custom-control-label universalradio-custom-control-label pl-2 pt-1" for="EnablePersonalInfoAllowed"></label></div>
                                                    <label class="form-check-label font-weight-normal" for="Enable">
                                                        Send Now<br/>
                                                        <small class="form-text text-muted">Immediate send your campaign</small>
                                                    </label>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                        <div class="form-group">
                                            <div class="form-control bg-transparent pl-0 border-0">
                                                <div class="form-check form-check-inline">
                                                    <div class="form-check form-check-inline custom-control mx-2 p-0 custom-radio"><input data-change-id="no" 
                                                        class="form-check-input check-PersonalInfoAllowed custom-control-input" 
                                                        v-on:change="SendType('Later')" name="rd5" id="DisablePersonalInfoAllowed" type="radio">
                                                        <label class="custom-control-label universalradio-custom-control-label pl-2 pt-1" 
                                                        for="DisablePersonalInfoAllowed"></label></div>
                                                    <label class="form-check-label font-weight-normal" for="Disable">
                                                        Send Later (Fixed time and Timezone)<br />
                                                        <small class="form-text text-muted">Setup a schedule to send campaign at a specific time</small>
                                                    </label>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left px-0" v-if="SendMultiple">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2 float-left px-1">
                                        <div class="form-group mt-2">
                                            <label>Multiple Days:</label>
                                            <div class="row mb-4">
                                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 mb-3 float-left" v-for="(itemSchedule,index) in SchedularArray"
                                                :key="itemSchedule">
                                                <div class="col-5 col-sm-5 col-md-5 col-lg-5 col-xl-5 float-left px-0">
                                                    <div class="input-group">
                                                        <v-date-picker :popover="popover" :min-date="getMinDate()" v-bind:name="'SchedulerList.Date_'+index"
                                                            :minute-increment="5" class="input-group" v-model="itemSchedule.CurrentDateZone">
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                <input v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em                                        class="fa fa-calendar"></em></span></div>
                                                            </template>
                                                        </v-date-picker>
                                                     </div>
                                                </div>
                                                    <div class="col-5 col-sm-5 col-md-5 col-lg-5 col-xl-5 float-left pr-0">
                                                        <div class="input-group">
                                                            <v-date-picker :popover="popover" :min-date="getMinDate()" v-bind:name="'SchedulerList.Time_'+index"
                                                            :minute-increment="5" class="timedate timedate_hs input-group" v-model="itemSchedule.CurrentTime" mode="time">
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                                <input placeholder="HH:MM" v-bind:class="{'form-control' : true}" :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em                                        class="fa fa-calendar"></em></span></div>
                                                            </template>
                                                        </v-date-picker>
                                                          </div>
                                                    </div>
                                                    <div class="col-2 col-sm-2 col-md-2 col-lg-2 col-xl-2 float-left">
                                                        <a href="javascript:;" data-toggle="modal" v-if="index==0" v-on:click="AddNewRow()" 
                                                        data-target="#adddesignation"><em class="fa fa-plus-circle text-dark" style="font-size: 30px;"></em></a>
                                                        <a href="javascript:;" data-toggle="modal" v-if="index>0" 
                                                            v-on:click="DeleteCurrentRow(index)" data-target="#adddesignation">
                                                            <em class="fa fa-trash text-danger" style="font-size: 28px;"></em></a>
                                                    </div>
                                                    <div class="col-9 col-sm-10 col-md-10 col-lg-10 col-xl-10 float-left mt-2 px-0">
                                                       <select v-model="itemSchedule.TimeZone" v-bind:class="{'form-control error_bodred':true}"  data-vv-as="Time Zone" 
                                                       v-bind:name="'SchedulerList.TimeZone_'+index">
                                                        <option value="">Select Time Zone</option>
                                                         <option v-for="zone in timezone" :key="zone" v-bind:value="zone.value">
                                                            {{zone.name}}
                                                         </option>
                                                        </select>
                                                    
                                                     </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                        </div>
                        </div>
                        <div class="modal-footer text-right border-0">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                            <a href="javascript:;" title=Close v-on:click="$emit('close')" class="btn btn-danger mr-2"><em class="fa fa-close pr-1"></em>{{$t('Close')}} </a>
                            <a href="javascript:;" v-on:click="Next(values,meta)" v-if="!SchedulerListForm"
                                class="btn btn-primary formbtn"><span>{{$t('Next')}}</span><em class="fa fa-angle-double-right pl-2"></em></a>
                            <a href="javascript:;" v-on:click="SaveSchedulerTime(values,meta)" v-if="SchedulerListForm"
                                class="btn btn-success formbtn"><em class="fa fa-save pr-1"></em>{{$t('Save')}} </a>
                        </div>
                        </div>
                    </Form>
                </div>
                
            </div>
        </div>
        <loader :is-visible="isLoading"></loader>
    </div>
    
</template>
<script>
import AddScheduleDataService from "../../../services/DataService";
import Pager from '../../../components/Common/Pager.vue'
export default {
    components: {
        Pager
        },
    data() {
        return {
            isLoading:false,
            editor: this.CLASSIC_EDITOR,
            EmailTemplateData: null,
            CurrentDateSelect: '',
            SendMultiple: false,
            timezone: [],
            SortBy: '',
            SortExp: '',
            activeKey: 1,
            PageNumber: 1,
            PageSize: 10,
            TotalRecords: 0,
            GroupStart: 1,
            GroupEnd: 3,
            CurrentPage: 1,
            ModelSortBy: '',
            ModelSortExp: '',
            PageSizeOption: null,
            NoRecordForModel: true,
            ListView: true,
            GridView: false,
            IsSelectGridDiv: null,
            SchedulerArrayList: {
                ModuleName: 'CRM',
                SubModuleCode:this.$route.params.subModuleCode,
                CampaignId: this.$route.params.id,
                templateId: '',
                TemplateContent: '',
                Subject: '',
                ReplyTo: '',
                Sender: '',
                TrackingCode: '',
                ContactListIds: [],
                SendType: 'SendNow',
                ScheduleSend: []
            },
            customTemplateId: '',
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            formName: 'TemplateList',
            TempListForm: true,
            ContactListForm: false,
            DetailListForm: false,
            SchedulerListForm: false,
            contactsListData: null,
            noRecordForTab: true,
            noRecordForTabGroup: true,
            Subject: '',
            ReplyTo: '',
            Sender: '',
            searchByEmailOrName: '',
            ContactListCheckBoxes: [],
            contactsGroupData: null,
            SchedularArray: [
                {
                    CurrentDateZone: this.CurrentDate('date'),
                    CurrentTime: '',
                    TimeZone: '',
                    templateId: ''
                }
            ],
            usertype: null,
            appUrl : process.env.VUE_APP_DEFAULT_APP_URL,
        }
    },
    created: function () {
        this.usertype = this.GetUserInfo.UserType
        this.FetchData();
    },
    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        }, 
        DeleteCurrentRow: function (item) {
            this.SchedularArray.splice(item, 1);
        },      
        SaveSchedulerTime: function () {
            var vm = this;
            vm.SchedulerArrayList.ScheduleSend = vm.SchedularArray;
            var checkValue = null;
            checkValue = vm.sendEmailScheduleModal;
                    var objectPostString = JSON.stringify(vm.SchedulerArrayList);
                        AddScheduleDataService.SaveEmailScheduler(objectPostString).then(function (response) {
                        if (response.data[0].Status == "Success") {
                            vm.$emit('close');
                            vm.$parent.loadData('schedule');
                            vm.ShowAlert(vm.$t('Status Saved Successfully',[vm.$t("Schedule Time")]), "success", true, vm.$t("Alert"));
                        }
                        else {
                            vm.$emit('close');
                            vm.ShowAlert(vm.$t("Error While Saving Data"),"failure",true,vm.$t('Alert'));
                        }
                    });
        },
        FetchData: function () {        
          var vm = this;
            var url = 'id=0&refId=' + vm.$route.params.id + '&subModuleCode=CRM_CAMPAIGNS&sortBy=' + vm.SortBy + '&sortExp=' + vm.SortExp + '&pageSize=' + vm.PageSize + '&pageNumber=' + vm.PageNumber;
            AddScheduleDataService.TemplateListing(url).then(function (response) {
                if (response.data == null || response.data == "") {
                    vm.NoRecordForModel = true;
                }
                else {
                    vm.EmailTemplateData = response.data.data;
                    var grepTemplateId = $.grep(vm.EmailTemplateData, function (element, index) {
                        if (index == 0) {
                            return element.TEMPLATE_ID;
                        }
                    });
                    vm.customTemplateId = grepTemplateId[0].TEMPLATE_ID;
                    vm.SchedulerArrayList.TemplateContent = grepTemplateId[0].TEMPLATE_CONTENT;
                    vm.SchedulerArrayList.templateId = grepTemplateId[0].TEMPLATE_ID;
                    setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                    setTimeout(function () { vm.RadioButtonBootstrap() }, 100);
                    vm.NoRecordForModel = false;

                }
            });
        },
        Next: function (values,meta) {
            var vm = this;
            switch(vm.formName){
                case "TemplateList":
                vm.$refs.TemplateList.validate().then((result) => {
                    if (result) {
                        vm.SchedulerArrayList.templateId = vm.customTemplateId;
                        if (vm.formName == 'TemplateList') {
                            vm.formName = 'ContactList';
                            vm.TempListForm = false;
                            vm.ContactListForm = true;
                            vm.loadData("ContactList");
                        }
                        else if (vm.formName == 'ContactList') {
                            if (vm.ContactListCheckBoxes.length >= 0) {
                                vm.SchedulerArrayList.Subject = vm.Subject;
                                vm.SchedulerArrayList.ReplyTo = vm.ReplyTo;
                                vm.SchedulerArrayList.Sender = vm.Sender;
                                vm.SchedulerArrayList.ContactListIds = vm.ContactListCheckBoxes;
                                vm.formName = 'DetailList';
                                vm.TempListForm = false;
                                vm.ContactListForm = false;
                                vm.DetailListForm = true;
                            }
                            else {
                                vm.ShowAlert(vm.$t("Select atleast one contact or group."), "warning", true, vm.$t("Alert"));
                                return false;
                            }
                        }
                        else if (vm.formName == 'DetailList') {
                            vm.formName = 'SchedulerList';
                            vm.TempListForm = false;
                            vm.ContactListForm = false;
                            vm.DetailListForm = false;
                            vm.SchedulerListForm = true;
                            vm.SchedularArray[0].CurrentDateZone = new Date();
                            vm.SchedularArray[0].TimeZone = '';
                            vm.SchedularArray[0].templateId = vm.customTemplateId;
                            vm.GetTimeZoneList();
                            
                        }
                    }
                })
                    break;
                case "ContactList":
                // vm.$refs.ContactList.setFieldTouched("Subject", true); 
                // vm.$refs.ContactList.setFieldTouched("Reply To", true); 
                // vm.$refs.ContactList.setFieldTouched("Sender", true);   
                vm.$refs.ContactList.validate().then((result) => {
                    if (result) {
                        vm.SchedulerArrayList.templateId = vm.customTemplateId;
                        if (vm.formName == 'TemplateList') {
                            vm.formName = 'ContactList';
                            vm.TempListForm = false;
                            vm.ContactListForm = true;
                            vm.loadData("ContactList");
                        }
                        else if (vm.formName == 'ContactList') {
                            if (vm.ContactListCheckBoxes.length >= 0) {
                                vm.SchedulerArrayList.Subject = vm.Subject;
                                vm.SchedulerArrayList.ReplyTo = vm.ReplyTo;
                                vm.SchedulerArrayList.Sender = vm.Sender;
                                vm.SchedulerArrayList.ContactListIds = vm.ContactListCheckBoxes;
                                vm.formName = 'DetailList';
                                vm.TempListForm = false;
                                vm.ContactListForm = false;
                                vm.DetailListForm = true;
                            }
                            else {
                                vm.$refs.form.setFieldTouched('Subject', true);
                                vm.ShowAlert(vm.$t("Select atleast one contact or group."), "warning", true, vm.$t("Alert"));
                                return false;
                            }
                        }
                        else if (vm.formName == 'DetailList') {
                            vm.formName = 'SchedulerList';
                            vm.TempListForm = false;
                            vm.ContactListForm = false;
                            vm.DetailListForm = false;
                            vm.SchedulerListForm = true;
                            vm.SchedularArray[0].CurrentDateZone = new Date();
                            vm.SchedularArray[0].TimeZone = '';
                            vm.SchedularArray[0].templateId = vm.customTemplateId;
                            vm.GetTimeZoneList();
                         
                        }
                    }
                })
                break;
                case "DetailList":
                // vm.$refs.DetailList.setFieldTouched("TrackingCode", true); 
                vm.$refs.DetailList.validate().then((result) => {
                    if (result) {
                        vm.SchedulerArrayList.templateId = vm.customTemplateId;
                        if (vm.formName == 'TemplateList') {
                            vm.formName = 'ContactList';
                            vm.TempListForm = false;
                            vm.ContactListForm = true;
                            vm.loadData("ContactList");
                        }
                        else if (vm.formName == 'ContactList') {
                            if (vm.ContactListCheckBoxes.length >= 0) {
                                vm.SchedulerArrayList.Subject = vm.Subject;
                                vm.SchedulerArrayList.ReplyTo = vm.ReplyTo;
                                vm.SchedulerArrayList.Sender = vm.Sender;
                                vm.SchedulerArrayList.ContactListIds = vm.ContactListCheckBoxes;
                                vm.formName = 'DetailList';
                                vm.TempListForm = false;
                                vm.ContactListForm = false;
                                vm.DetailListForm = true;
                            }
                            else {
                                vm.$refs.form.setFieldTouched('Subject', true);
                                vm.ShowAlert(vm.$t("Select atleast one contact or group."), "warning", true, vm.$t("Alert"));
                                return false;
                            }
                        }
                        else if (vm.formName == 'DetailList') {
                            vm.formName = 'SchedulerList';
                            vm.TempListForm = false;
                            vm.ContactListForm = false;
                            vm.DetailListForm = false;
                            vm.SchedulerListForm = true;
                            vm.SchedularArray[0].CurrentDateZone = new Date();
                            vm.SchedularArray[0].TimeZone = '';
                            vm.SchedularArray[0].templateId = vm.customTemplateId;
                            vm.GetTimeZoneList();
                          
                        }
                    }
                })
                    break;
            }                     
        },
        GetTimeZoneList: function () {
            var vm = this;
           var url = "id=&moduleName=CRM&type=TIMEZONE&search=&refId="
           AddScheduleDataService.getDDLData(url).then(function (response) {
                vm.timezone = response.data.DATA;
            });
        },
        pagerMethod: function (value) {                          
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.loadData("ContactList");
        },
        loadData(tabName) {
            if (tabName != "" && tabName != null) {
                var vm = this;
                if (tabName == "ContactList") {
                    vm.isLoading=true;
                    var url = 'id=' + vm.$route.params.id + '&subModuleCode=CRM_CAMPAIGNS&First_Name=' + vm.searchByEmailOrName + "&pageSize=" + vm.PageSize + "&pageNumber=" + vm.PageNumber + "&sortBy=" + vm.SortBy + "&sortExp=" + vm.SortExp + "&statusId=1001&requestType=popup"
                    AddScheduleDataService.ContactListing(url).then(function (response) {
                        vm.isLoading=false;
                        vm.activeKey = 1;
                        if (response.data != null && response.data.data.length > 0) {
                            vm.contactsListData = response.data.data;
                            vm.TotalRecords = response.data.data[0].total_records;
                            vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                            vm.CurrentPage = vm.PageNumber;
                            setTimeout(function () {
                                $("#Schedule_contactList input[type=checkbox]").prop("checked", false);
                                $.each(vm.ContactListCheckBoxes, function (index, selectedcontactlist) {
                                    var rtnObj = $.grep(vm.contactsListData, function (element, index) {
                                        return element.contact_id == selectedcontactlist.id;
                                    });
                                    if (rtnObj.length > 0) {
                                        $("#Schedule_contactList input[type=checkbox][value='" + rtnObj[0].contact_id + "']").prop("checked", true);
                                    }
                                });
                                var checkBoxLength = $("#Schedule_contactList input[type=checkbox]:checked").not("[id^='chkAll']").length;
                                if (vm.contactsListData.length == checkBoxLength) {
                                    $("#Schedule_contactList [id^='chkAll']").prop("checked", true).attr('checked');
                                }
                                else {
                                    $("#Schedule_contactList [id^='chkAll']").prop("checked", false).removeAttr('checked');
                                }
                               
                            }, 100);
                            setTimeout(function () {
                            vm.ResponsiveDataTable('Schedule_contactList');
                        }, 500);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.contactsListData = null;
                            vm.TotalRecords = 0;
                            
                        }
                    });
                   
                }
                else if (tabName == "ContactGroup") {
                    var vm = this;
                    vm.isLoading=true;
                    var url = "id=" + vm.$route.params.id + "&subModuleCode=CRM_CAMPAIGNS";
                    AddScheduleDataService.ContactGroupListing(url).then(function (response) {
                        vm.isLoading=false;
                        vm.activeKey = 2;
                        if (response.data.data.length > 0) {
                            vm.contactsGroupData = response.data.data;
                            setTimeout(function () {
                                $("#Schedule_contactGroupList input[type=checkbox]").prop("checked", false);
                                $.each(vm.ContactListCheckBoxes, function (index, selectedcontactlist) {
                                    var rtnObj = $.grep(vm.contactsGroupData, function (element, index) {
                                        return element.GROUP_ID == selectedcontactlist.id;
                                    });
                                    if (rtnObj.length > 0) {
                                        $("#Schedule_contactGroupList input[type=checkbox][value='" + rtnObj[0].GROUP_ID + "']").prop("checked", true);
                                    }
                                });
                                var checkBoxLength = $("#Schedule_contactGroupList input[type=checkbox]:checked").not("[id^='chkAll']").length;
                                if (vm.contactsGroupData.length == checkBoxLength) {
                                    $("#Schedule_contactGroupList [id^='chkAll']").prop("checked", true).attr('checked');
                                }
                                else {
                                    $("#Schedule_contactGroupList [id^='chkAll']").prop("checked", false).removeAttr('checked');
                                }
                               
                            }, 100);
                            setTimeout(function () {
                            vm.ResponsiveDataTable('Schedule_contactGroupList');
                        }, 500);
                        }
                        else {
                          
                            vm.contactsGroupData = null;
                           
                            setTimeout(function () { vm.RadioButtonBootstrap() }, 100);
                        }
                    });
                    
                }
               
            }
          
        },
        RadioChange: function (item, index) {

            var vm = this;
            vm.IsSelectGridDiv = index;
            this.customTemplateId = item.TEMPLATE_ID;
            this.SchedulerArrayList.TemplateContent = item.TEMPLATE_CONTENT;

        },
        SendType: function (type) {
            if (type == 'Later') {
                this.SendMultiple = true;
                this.SchedulerArrayList.SendType = 'SendLater'
                this.SchedularArray[0].CurrentDateZone = this.CurrentDate('date');
            }
            else {
                this.SendMultiple = false;
                this.SchedularArray = [
                   {
                       CurrentDateZone: this.CurrentDate('date'),
                       CurrentTime: '',
                       TimeZone: '',
                       templateId: this.SchedulerArrayList.templateId
                   }
                ];

                this.SchedularArray[0].CurrentDateZone = new Date();
                this.SchedularArray[0].TimeZone = '';
                this.GetTimeZoneList();
                this.SchedulerArrayList.SendType = 'SendNow'
            }
        },
        Cross: function (item, index) {
            var vm = this;
            if (item.type == "List") {
                $("#Schedule_contactList [id^='chkAll']").prop("checked", false).removeAttr('checked');
                $("#Schedule_contactList input[type=checkbox][value='" + item.id + "']").prop("checked", false);
            }
            else {
                $("#Schedule_contactGroupList [id^='chkAll']").prop("checked", false).removeAttr('checked');
                $("#Schedule_contactGroupList input[type=checkbox][value='" + item.id + "']").prop("checked", false);
            }
            vm.ContactListCheckBoxes.splice(index, 1);
        },
        AddNewRow: function () {
            var obj = {
                CurrentDateZone: this.CurrentDate('date'),
                CurrentTime: '',
                TimeZone: '',
                templateId: this.SchedulerArrayList.templateId
            }
            this.SchedularArray.push(obj);
            this.GetTimeZoneList();
            this.$validator.reset();
        },
        SelectedDiv: function (item, index) {

            var vm = this;
            vm.IsSelectGridDiv = index;

            this.customTemplateId = item.TEMPLATE_ID;
            this.SchedulerArrayList.TemplateContent = item.TEMPLATE_CONTENT;
        },
        ListCheckBox: function (item, event, type) {
          
            var vm = this;
            if (event.target.checked) {
                var obj = {
                    id: (type == "Group" ? item.GROUP_ID : item.contact_id),
                    type: type,
                    name: (type == "Group" ? item.NAME : item.first_name + " " + item.last_name),
                    templateId: vm.SchedulerArrayList.templateId
                }
                vm.ContactListCheckBoxes.push(obj);
            }
            else {
                vm.ContactListCheckBoxes.splice(vm.ContactListCheckBoxes.findIndex(function (checkItem) { return checkItem.id == (type == "Group" ? item.GROUP_ID : item.contact_id) }), 1);
            }
        },

        CurrentDate: function (format) {
            var newDate = new Date();
            if (format == 'date') {
                var month = newDate.getMonth() + 1;
                var year = newDate.getFullYear();
                var day = newDate.getDate();
                var stringDate = "";
                if (month < 10) {
                    month = '0' + month;
                }
                stringDate = 'MM-DD-YYYY'

                return stringDate;
            }
            if (format == 'time') {
                return  'HH:MM';
            }
            return newDate;
        },
        ChangeView: function (view) {
            var vm = this;
            if (view == 'List') {
                vm.GridView = true;
                vm.ListView = false;
            }
            else if (view == 'Grid') {
                vm.GridView = false;
                vm.ListView = true;
            }
            setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
            setTimeout(function () { vm.RadioButtonBootstrap() }, 100);
        },
        ListAllCheckBox: function (event, type) {
            var vm = this;
            if (type == "List") {
                $('.listall.chkItems:checkbox').not("[id^='chkAll']").each(function () {
                    var firstEachObj = this;
                    if ($(this).prop("checked") == true) {
                        var obj = {
                            id: $(this).val(),
                            type: 'List',
                            name: $(this).attr('data-firstname') + " " + $(this).attr('data-lastname'),
                            templateId: vm.SchedulerArrayList.templateId
                        }
                        var rtnObj = $.grep(vm.ContactListCheckBoxes, function (element, index) {
                            return element.id == $(firstEachObj).val();
                        });

                        if (rtnObj.length == 0) {
                            vm.ContactListCheckBoxes.push(obj);
                        }
                    } else {
                        var chekInd = vm.ContactListCheckBoxes.findIndex(function (checkItem) { return checkItem.id == $(firstEachObj).val() });
                        if (chekInd >= 0) {
                            vm.ContactListCheckBoxes.splice(chekInd, 1);
                        }
                    }
                });
            }
            else {
                $('.groupAll.chkItems:checkbox').not("[id^='chkAll']").each(function () {
                    var obj = {
                        id: $(this).val(),
                        type: 'Group',
                        name: $(this).attr('data-name'),
                        templateId: vm.SchedulerArrayList.templateId
                    }
                    var firstEachObj = this;
                    if ($(this).prop("checked") == true) {
                        var rtnObj = $.grep(vm.ContactListCheckBoxes, function (element, index) {
                            return element.id == $(firstEachObj).val();
                        });
                        if (rtnObj.length == 0) {
                            vm.ContactListCheckBoxes.push(obj);
                        }
                    } 
                    else {
                        vm.ContactListCheckBoxes.splice(vm.ContactListCheckBoxes.findIndex(function (checkItem) { return checkItem.id == $(firstEachObj).val() }), 1);
                    }
                });
            }
        }
    },
}
</script>