<script>
import LeadDataService from "../../../services/DataService";
import Pager from '../../../components/Common/Pager.vue'


export default {
    props: {
        sourceid: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
       
        modulename: {
            type: String,
            required: true
        },
        sourcename: {
            type: String,
            required: true
        },
    
       
    },
    components: {
        Pager,
    },
    data: function () {
        return {
            isLoading: false, 
            subModuleCode: '',
            sourceId: '',
            ModelContactData: null,
            ModelHeaders: null,
            ProductCheckBoxes: [],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            ModelSortBy: '',
            ModelSortExp: '',
            PageSizeOption: null,
            CurrentPage: 1,
            TotalPages: 0,
            GroupStart: 1,
            GroupEnd: 3,
            noRecord: false,
            searchByEmailOrName: '',
            isContactSelected: false,
            errorMessageForProduct: '',
            checkButtonClick: false,
        }
    },
    created: function () {
        this.FetchData();
        this.GetPageSizeOption();
    },
    methods: {
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        pagerMethod: function (value) {
               
               this.PageSize = value.PageSize;
               this.PageNumber = value.PageNumber;
               this.FetchData();
           },
        GetLocalizedValue: function (fieldName, displayName) {
            if (this.isBlank(displayName)) {
                var chk = this.$t(String(fieldName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return fieldName;
            }
            else {
                var chk = this.$t(String(displayName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return displayName;
            }
        },
        SetCurrentPage: function (pageNumber) {
            this.PageNumber = pageNumber;
            if (pageNumber > this.GroupEnd) {
                this.GroupStart = pageNumber;
                this.GroupEnd = pageNumber + 2;
            }
            this.FetchData();
        },
        sortdata: function (sortBy) {
            this.ModelSortBy = sortBy;
            var sortExp = 'ASC';
            if (this.ModelSortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.ModelSortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        SetPageSize: function (event) {
            this.PageSize = event.target.value;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        },
        addContact: function () {
            
     var vm = this;
            vm.isContactSelected = false;
            vm.errorMessageForProduct = '';
            var contactIds = '';
            vm.checkButtonClick = true;
            $("#divshowContactList table tr td input[type='checkbox']:checked").each(function () {
                if (contactIds.length > 0) {
                    contactIds += ",";
                }
                contactIds += $(this).val();
            });
            if (contactIds.length > 0) {
                var ContactMappingData = {
                    sourceId: vm.sourceid,
                    moduleName: vm.modulename,
                    subModuleCode: vm.submodulecode,
                    sourceName: vm.sourcename,
                    contactIds: contactIds
                };
                var objectPostString = JSON.stringify(ContactMappingData);
                LeadDataService.SaveExistingContactWithMapping(objectPostString).then(function (response) {
                  
                    if (response.data[0].Status == "Success") {
                        
                        vm.ShowAlert(vm.$t("Contacts are successfully associated"), "success", true, vm.$t("Alert"));
                        vm.$emit('close');
                        vm.$parent.loadData('contacts');
                        vm.checkButtonClick = false;
                    }
                     
                });
            }
            else {
                vm.checkButtonClick = false;
                vm.isContactSelected = true;
                vm.errorMessageForProduct = 'Please select any contact first.';
            }
        },
        cancelOrRefresh: function () {
            this.searchByEmailOrName = "";
            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
            this.FetchData();
        },
        FetchData: function (hittype) {
            this.isLoading=true; 
            var vm = this;
            if (typeof (hittype) == 'undefined')
                hittype = "";
            var url = "id=" + vm.sourceid + "&subModuleCode=" + vm.submodulecode + "&First_Name=" + vm.searchByEmailOrName + "&pageSize=" + vm.PageSize + "&pageNumber=" + (hittype == 'search' && vm.searchByEmailOrName!=''?1:vm.PageNumber) + "&sortBy=" + vm.ModelSortBy + "&sortExp=" + vm.ModelSortExp + "&statusId=1001&requestType=popup";
           LeadDataService.ContactListing(url)
                .then(function (response) {
                    vm.isLoading=false;
                    if (response.data != null && response.data.data.length > 0) {
                        vm.ModelContactData = response.data.data;
                        vm.ModelHeaders = response.data.schema;
                        vm.TotalRecords = response.data.data[0].total_records;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        vm.noRecord = false;
                    }
                    else {
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                    }                   
                    setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                    //setTimeout("SliderDivListingScrool()", 100);
                });
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        openContactModals:function (contactDetails) {
            var vm = this;
            vm.$emit("close");        
            vm.$parent.openCRMContactModal(null); 
        },
    },
}
</script>

<template>
 
   
        <div class="modal d-block my-popups dialog">
            <loader :is-visible="isLoading"></loader>
        <div class="modal-dialog">
        <div class="modal-content">
        <div class="modal-header">
        <slot name="header"><h5 class="modal-title"> {{$t("Contacts")}}  </h5></slot>
        <button v-on:click="$emit('close')" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>
        </div>
        <div class="modal-body">
        <div class="form_content">
    
        <div class="form-content mb-3">
        <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
       
        <div class="col-sm-12 col-md-6 col-lg-8 col-xl-8 float-left p-0">
        <div class="form-group mb-0">
      
        <input v-model.trim="searchByEmailOrName" class="form-control" type="text" :placeholder="$t('searchByNameOrEmail')"/>
        </div>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-4 col-xl-4 float-left">
        <div class="form-group">
        <div class="search-btm-btn">
        <a href="javascript:;" v-on:click="FetchData('search')" class="btn btn-success mr-2"><em class="fa fa-search"></em></a>
        <a href="javascript:;" class="btn btn-danger" v-on:click="cancelOrRefresh()"><em class="fa fa-refresh"></em></a>
        </div>
        </div>
        </div>
     
        </div>
        </div>
        </div>
        </div>
        <div id="divshowContactList" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
        <div class="form-group is-invalid" v-if="isContactSelected">
        <div class="invalid-feedback alert alert-danger" role="alert" style="display:block;">{{errorMessageForProduct}} 
        <a href="javascript:;" v-on:click="isContactSelected= false"><em class="fa fa-close float-right text-danger"></em></a>
        </div>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
        <div class="heading-border">
        <h5 class="h5"><span> {{$t("Contacts") }} </span> <small class="float-right">
        <a href="javascript:void(0);" title="" id="ancmergeloaddata" v-on:click="openContactModals(null)" class="mr-2"><em class="fa fa-plus pr-1"></em>{{$t("AddContact") }} </a>

        </small></h5>
        </div>
        </div>
        <div class="listing mt-2">
        <div class="table-responsive tableborder popuptablescroll_365 table200th" style="max-height:550px!important;overflow-x:auto">
        <table class="table-sorting table table-bordered m-0" v-if="!noRecord" aria-describedby="existcontact">
        <thead class="thead-bg">
        <tr>
         <th scope="col" style="width:50px !important;" class="text-center"><input id="chkAll" class="chkitems" value="" type="checkbox"></th>
         <th scope="col" v-for="item in ModelHeaders"><span v-if="item.VISIBLE" 
            v-bind:class="{sort: item.SORTABLE, tb_headerSortUp :  ModelSortExp=='ASC' && ModelSortBy==item.COLUMN_NAME, tb_headerSortDown :  ModelSortExp=='DESC' && ModelSortBy==item.COLUMN_NAME}" 
            v-on:click="sortdata(item.COLUMN_NAME)" v-bind:id="item.COLUMN_NAME">{{GetFormSchemaFieldName(item.COLUMN_NAME, item.DISPLAY_NAME)}}</span></th>
            
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in ModelContactData">
        <td class="text-center"><input class="chkItems" v-bind:value="item.contact_id" type="checkbox"></td>
        <td v-for="key in ModelHeaders"  v-bind:class="{'text-center':key.COLUMN_NAME=='status_name'}">
            <template v-if="key.VISIBLE">
                <span v-if="(key.DATA_TYPE=='date' || key.DATA_TYPE=='datetime')" v-bind:class="{'px-2 bg-info text-white d-block': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Inactive')}">{{item[key.COLUMN_NAME] | formatDate}}</span>
        <span v-else="(key.DATA_TYPE!='date' && key.DATA_TYPE=='datetime')" v-bind:class="{'px-2 bg-info text-white d-block': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block': (key.COLUMN_NAME=='status_name' && item[key.COLUMN_NAME] == 'Inactive')}">{{item[key.COLUMN_NAME]}}</span>

            </template>
                </td>
        </tr>
        </tbody>
        </table>
        <div v-if="noRecord" class="text-center text-danger" style="padding:20px;"> {{$t("NoRecordfound") }} </div>
        </div>
        <div class="border-top-pagi">
        <Pager v-bind:numberofrecords="TotalRecords" prev-text="Prev" first-text="First" last-text="Last" next-text="Next" v-on:SetCurrentPage="pagerMethod"></Pager>
        </div>
        <div>
        <small class="float-right mt-2">
        <a id="addModelProduct" href="javascript:;" :title=" $t('Associate')" v-on:click="(!checkButtonClick)?addContact():''"
         v-bind:class="{'disabled':checkButtonClick}"  class="btn btn-success text-white"><em class="fa fa-plus pr-1"></em>{{ $t("Associate")}} </a>
        </small>
        </div>
        </div>
        </div>
        </div>
        </div>
       
        </div>
        </div>
      
      
       
</template>