<template>
     <div>
       
        <div class="listing" v-if="!noRecordForTab">
        <div class="table-responsive table-fix-header">
        <table class="table-sorting table table-bordered mb-0"  id="table-contacts-content" aria-describedby="crmcontact">
        <thead class="thead-bg">
        <tr>
         <th scope="col" style="width:50px !important;" v-if="submodulecode=='CRM_CAMPAIGNS'" class="text-center"><input id="chkAll" class="chkitems" value="" type="checkbox"></th>
         <th scope="col"  class="text-capitalize text-left" style="padding: 10px 8px !important;"><span>Name</span></th>
        <template v-for="(item,itemIndex) in ContactsHeader">
             <th scope="col" v-if="item.VISIBLE && (item.COLUMN_NAME !='first_name' && item.COLUMN_NAME !='last_name' && item.COLUMN_NAME !='gender')"
              class="text-capitalize text-left" v-bind:style="bindStyle(item.COLUMN_NAME)"  :key="itemIndex">
            <span v-bind:id="item.COLUMN_NAME">{{GetLocalizedValue(item.COLUMN_NAME, item.DISPLAY_NAME)}}</span>
            </th>
        </template>          
         <th scope="col" style="min-width:50px;" class="text-center" v-if="submodulecode=='CRM_CAMPAIGNS'"><span>{{$t('ConvertLead')}}</span></th>
         <th scope="col" style="min-width:50px;" class="text-center" v-if="submodulecode=='CRM_LEADS' "><span>Is Primary</span></th>
         <th scope="col" style="min-width:50px;" class="text-center" v-if="submodulecode=='CRM_CONTRACTS'"><span>Gender</span></th>
         <th scope="col" style="min-width:50px;" class="text-center"><span>DeAssociate</span></th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,index) in ContactsData">
        <td  :key="index" class="text-center" v-if="submodulecode=='CRM_CAMPAIGNS'"><input class="chkItems" v-bind:disabled="item.converted_lead_id!=null" v-bind:value="item.contact_id" type="checkbox"></td>
        <td v-bind:class="{'text-left':true}">
        <a href="javascript:;" class="actions-onclick" v-on:click="openContactModal(item['contact_id'])"><span class= "text"  style="color:dodgerblue;cursor: pointer;">{{item.first_name}} {{item.last_name}}</span></a>
        </td>
        <template v-for="(key,KeyIndex) in ContactsHeader">
        <td  :key="KeyIndex" v-bind:class="{'text-left':true, 'text-center':key.COLUMN_NAME=='status_id'}"
        v-if="key.VISIBLE && (key.COLUMN_NAME !='first_name' && key.COLUMN_NAME !='last_name' && key.COLUMN_NAME !='gender')">        
        <span v-if="(key.DATA_TYPE=='date' || key.DATA_TYPE=='datetime')" v-bind:class="{'px-2 bg-success text-white d-block': (key.COLUMN_NAME=='status_id' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block': (key.COLUMN_NAME=='status_id' && item[key.COLUMN_NAME] == 'Inactive')}">{{item[key.COLUMN_NAME] | formatDate}}</span>
        <span v-else-if="(key.DATA_TYPE!='date' && key.DATA_TYPE!='datetime')" v-bind:class="{'px-2 bg-success text-white d-block': (key.COLUMN_NAME=='status_id' && item[key.COLUMN_NAME] == 'Active'),'px-2 bg-danger text-white d-block': (key.COLUMN_NAME=='status_id' && item[key.COLUMN_NAME] == 'Inactive')}">{{(key.COLUMN_NAME=='gender')?((item[key.COLUMN_NAME]=='M')?'Male':(item[key.COLUMN_NAME]=='F')?'Female':''):item[key.COLUMN_NAME]}}</span>
        </td>
        </template>
       
      
        <td v-if="submodulecode=='CRM_CAMPAIGNS'" class="text-center single-action"><a
                v-if="submodulecode=='CRM_CAMPAIGNS' && item.converted_lead_id==null"
                v-on:click="ChangeLeadOwner(item.contact_id)" href="javascript:;" class="pop" typefor="ConvertLead"
                :typetitle="$t('ConvertLeaded')" value="item.contact_id" :title="$t('ConvertLead')"> <em class="fa fa-retweet text-primary action-icon"></em></a> <span
                v-if="submodulecode=='CRM_CAMPAIGNS' && item.converted_lead_id!=null"><em class="fa fa-retweet disabled action-icon"></em></span></td>

        <td v-if="submodulecode=='CRM_LEADS' ">
        <div class="form-group text-center mb-0" >
       <label v-bind:class="{'switch':true,'switch disabled' :(LeadStatus=='Closed' || item.IsPrimary==true)}">
        <input type="checkbox" v-model="item.IsPrimary" v-bind:name="'IsPrimary'+index" v-bind:checked="item.IsPrimary" v-bind:id="'IsPrimary'+index" v-on:change="SetLoginOrPrimary(item,$event)" class="form-check-input rdoticketfor custom-control-input dynamic">
        <span class="slider round">
        <span class="slider-yes">Yes</span> 
        <span class="slider-no">No</span>
        </span>
        </label>
        </div >
        </td> 
        <td class="text-center"  v-if="submodulecode=='CRM_CONTRACTS'">{{item.gender}}</td>
        <td class="text-center single-action"><a href="javascript:;" v-bind:class="{'disabled':item.IsPrimary || item.converted_lead_id!=null}" 
        v-on:click="deleteContactFromTab(item['contact_id'])" :title="$t('DeAssociate')"><em class="fa fa-times action-icon text-danger pr-2" v-bind:class="{'disabled':item.IsPrimary || item.converted_lead_id!=null}"></em></a></td>
       </tr></tbody></table></div></div><div v-if="noRecordForTab" class="text-center text-danger p-4 border">  {{$t('NoRecordfound')}}</div>
       <modalleadowner v-if="showLeadOwnerModal" :OwnerIds="OwnerIds" ModuleType="Campaign" @close="onCloseLeadOwner">
        <h5 slot="header">{{$t('LeadOwner')}}</h5>
    </modalleadowner>
    </div>
       
</template>
<script>
import LeadDataService from "../../../services/DataService";
import modalleadowner from '../../../components/Common/Modal/modalleadowner.vue'
import $ from "jquery";
export default {
    components: {
            modalleadowner
        },
        props: {
        contactsdata: {
            type: Array,
            required: true
        },
        contactsheader: {
            type: Array,
            required: true
        },
        sourceid: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        modulename: {
            type: String,
            required: true
        }
    },
    data: function () {
        return {
            ContactsData: [],
            noRecordForTab: true,
            LeadStatus: '',
            ContactsHeader: [],
            RecordCount: 0,
            OwnerIds: [],
            ModuleType:'',
            showLeadOwnerModal: false
        }
    },
    watch: {
        contactsheader: function (value) {
            this.ContactsHeader = value;
        },
        contactsdata: function (value) {
            var vm=this;
            vm.ContactsData = value;
            try {
                this.LeadStatus = this.$parent.LeadStatus;
                this.RecordCount = this.ContactsData.length;
            } catch (ex) { console.log(ex)}
            vm.noRecordForTab = ((vm.ContactsData != null && vm.ContactsData.length > 0) ? false : true);           
            setTimeout(function(){vm.ResponsiveDataTable('table-contacts-content')}, 500);
        }
    },
    methods: {
        ChangeLeadOwner: function (id) { 
                var varObj = this;  
                varObj.OwnerIds.push(id);
                if (varObj.OwnerIds.length > 0) {
                    varObj.showLeadOwnerModal = (varObj.showLeadOwnerModal) ? false : true;
                }          
            },   
        openContactModal: function (contactDetails) {
          var vm=this;
            var type = 'AddContact';
            if (contactDetails != null) {
                type = 'UpdateContact';
            }
            if ((typeof this.$parent.checkForAdditionalPermission !== 'undefined') && !this.$parent.checkForAdditionalPermission(type)) {
                vm.confirmR(vm.$t("additionalOwnerAuthorityWarning"), true, vm.$t('Alert'));
            } else {
                this.$parent.iscontactDetails = contactDetails;
                this.$parent.showContactModal = (this.$parent.showContactModal) ? false : true;
            }
        },
        deleteContactFromTab: function (value) {
            var vm=this;
            if ((typeof this.$parent.checkForAdditionalPermission !== 'undefined') && !this.$parent.checkForAdditionalPermission('DeleteContact')) {
                vm.confirmR(vm.$t("additionalOwnerAuthorityWarning"), true, vm.$t('Alert'))
            } else {
                var ths = this;
                    if (value > 0) {
                        ths.confirmR(ths.$t("ConfirmDeAssociateCommonContact"), true, null, function () {                                    
                            var postJSON = {
                                ids: value,
                                moduleName: ths.modulename,
                                subModuleCode: ths.submodulecode,
                                refCode: 'DELETE_VIEW_CONTACTS',
                                refId: ths.sourceid
                            };
                            var objectPostString = JSON.stringify(postJSON);
                           LeadDataService.CommonDelete(objectPostString).then(function (response) {                              
                                var box = "";
                                if (response.data[0].Status == "Success") {
                                    $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                    ths.$parent.loadData('contacts');
                                    box = ths.ShowAlert(ths.$t('DeassociateSuccess',[ths.$t('Contact')]), "success", true, ths.$t("Alert"));
                                }
                                else {
                                    box = ths.ShowAlert(ths.$t('ErrorDeassociateFailure',[ths.$t('Contact')]), "failure", true, ths.$t("Alert"));
                                }
                                setTimeout(function () {
                                    box.modal('hide');
                                }, 2000);                                 
                            });                        
                    });
                }
                else {
                    ths.ShowAlert(ths.$t('DeletedError', [ths.$t('Contact')]), "failure", true, this.$t("Alert"));
                }
            }
        },
        bindStyle: function (item) {
            var styleObj = {};
            if (item == "personal_notes") {
                styleObj = {
                    'min-width': '200px',
                }
            }
            else {
                styleObj = {
                    'min-width': '120px',
                }
            }
            return styleObj;
        },
        
        SetLoginOrPrimary: function (itm, event) {
            var vm = this;
            var IsPrimary = event.currentTarget.name.indexOf("IsPrimary");
            var message = "";
            message = "Are you sure you want to change primary?";
            talygen.confirm({
                title: '',
                    message: message,
                    callback: function (result) {
                        if (result) {
                            $.each(vm.ContactsData, function (index, item) {
                                if (item.contact_id != itm.contact_id) {
                                    console.log("item", item);
                                    item.IsPrimary = false;
                                }
                            });
                        vm.SaveContactAsPrimary(itm)
                        }
                        else {
                            itm.IsPrimary = !itm.IsPrimary;
                        }
                    }
                });
        },
        SaveContactAsPrimary: function (item) {
            this.deleteContactFromTab
            var vm = this;
            var JsonObject = {
                contact_id: item.contact_id,
                IsPrimary: item.IsPrimary,
                Type: 'LEAD',
                refId: vm.sourceid
            }
            var objectstring = JSON.stringify(JsonObject);
                vm.IsLogin = item.IsLogin;
               LeadDataService.SaveContactAsPrimary(objectstring).then(function (response) {
              
                    if (response.data != "") {
                        if (response.data.msg == "SUCCESS") {
                            vm.ShowAlert(vm.$t("Contact Updated successfully."), "success", true, vm.$t("Alert"));
                            vm.$parent.loadData('contacts');
                        }
                    }
                     
                });           
        },
        onCloseLeadOwner(){
            this.showLeadOwnerModal = false;
            this.$parent.loadData('contacts');
        }
    },
}
</script>