<script>
import LeadDataService from "../../../services/DataService";
export default {
    props:{
        OwnerIds:null,
        ModuleType:''
    },
     data: function () {
            return {
                leadid: '',
                LeadownerArray: [],
                SubModuleCode: "CRM_LEADS"
            }
        },
        created: function () {
            this.GetLeadOwnerlist();
            if(this.ModuleType == "Deal"){
                this.SubModuleCode= "CRM_DEALS";
            }
        },
        methods: {
            GetLeadOwnerlist: function () {
                var vm = this;
                var url = "id=&moduleName=CRM&type=CRM_USERS";
                 
               
              LeadDataService.getDDLData(url) .then(function (response) {
                    var json = response.data;
                    if (json != null) {
                        vm.LeadownerArray = json.DATA;
                        return vm.LeadownerArray;
                    }
                     
                });
            },
            submitChangeOwner: function () {
                var vm = this;
                console.log(vm.$parent);
                // vm.$refs.form.setFieldTouched('name', true);  
                vm.$refs.form.validate().then((result) =>  {
                    if (result) {
                        if(vm.ModuleType=="Campaign"){
                       var LeadDetail={
                        leadOwner_id:vm.leadid,
                        campaign_id:vm.$route.params.id,
                        ModuleName:vm.$route.params.moduleName,
                        subModule_Code:vm.$route.params.subModuleCode,
                        contact_id:vm.OwnerIds.toString()
                       }
                       var objectPostString = JSON.stringify(LeadDetail);                      
                        LeadDataService.ConvertToLeadCampaign(objectPostString)
                        .then(function (response) {
                            if (response.data[0].Status == "Success") {                           
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.ShowAlert("Contact has been converted into lead successfully", "success", true, vm.$t("Alert"));
                            vm.$emit('close');
                        }
                        else {
                            vm.ShowAlert("Something went wrong. Please retry!");
                        }
                        })
                        }
                        else{
                        var data = {
                            sourceId: vm.activitySourceID,
                            moduleName: "CRM",
                            subModuleCode: vm.SubModuleCode,
                            newleadID: vm.leadid,
                            checkedIds: vm.OwnerIds,
                            ModuleType: vm.ModuleType
                        };                        
                        var objectPostString1 = JSON.stringify(data);
                       LeadDataService.ChangeOwner(objectPostString1).then(function (response) {
                            if (response.data[0].Status == "Success") {
                                vm.ShowAlert(vm.$t('LeadOwnerChanged',[vm.ModuleType == "Deal"?vm.$t('DealOwner'): vm.$t('LeadOwner')]),"success", true,  vm.$t('Alert'));                           
                                vm.$emit('close');
                            }
                        });          
        }
    }
      });
            }
}}
 
</script>

<template>
    <transition name="modal">
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">
                                {{$t("LeadOwner")}}
                            </h5>
                            <button v-on:click="$emit('close')" href="javascript:;" class="close"><em class="fa fa-times"
                                    aria-hidden="true"></em></button>
                        </div>
                        <div class="modal-body">
                            <Form ref="form">
                                <div class="d-flex flex-wrap">
                                    <div class="col-md-12 p-0">
                                        <div class="row">
                                            <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                                <div class="form-group">
                                                    <label> {{$t("SelectLeadOwner")}}:<span
                                                            class="text-danger">*</span></label>
                                                            <Field id="ddl_changeOwner" 
                                                            :rules="{'required':true}" label="name"
                                                            v-slot="{ errors }">
                                                            <select :class="{'form-control':true,'is-invalid':errors[0]}" v-model="leadid">
                                                                <option value="">{{$t("Select")}} {{$t("OWNER")}}</option>
                                                            <option v-for="(Owner,index) in LeadownerArray" :key="index"
                                                                v-bind:value="Owner.value">
                                                                {{Owner.name}}
                                                            </option>
                                                            </select>   
                                                                                                                  
                                                        </Field>
                                                                                                                                                            
                                                   </div>
                                            </div>
                                        </div>
                                        <div class="row flex-row-reverse">
                                            <div class="col-lg-6 text-right">
                                                <button type="button" class="btn btn-primary save-btn mr-1"
                                                    v-on:click="submitChangeOwner"><em class="fa fa-save pr-2"
                                                        aria-hidden="true"></em>{{$t("Save")}}</button>
                                                <button type="button" class="btn btn-primary delete-btn"
                                                    v-on:click="$emit('close')" data-dismiss="modal"> <em
                                                        class="fa fa-times" aria-hidden="true"></em>
                                                    {{$t("Cancel")}}</button>
                                            </div>
                                            <div class="col-lg-6">
                                                <div class="py-2">
                                                  
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>