              if (response.data[0].Status == "Success") {
                vm.ShowAlert(
                  vm.$t("DeletedSuccess", [vm.$t("AdditionalOwner")]),
                  "success",
                  true,
                  vm.$t("Alert")
                );
                vm.loadData("additionalleadowners");
              } else {
                vm.ShowAlert(
                  vm.$t("DeletedError", [vm.$t("AdditionalOwner")]),
                  "failure",
