      var vm = this;
      if (vm.$route.params.id > 0) {
        vm.confirmR(
          this.$t("AreYouSuretoReopenLead"),
          true,
          null,
          function (result) {
            var postJSON = {
              ids: `${vm.$route.params.id}`,
            };
            var objectPostString = JSON.stringify(postJSON);
            CampaignDataService.reopenLead(objectPostString).then(function (
              response
            ) {
              var box = "";
              if (response.data[0].Status == "Success") {
                if (
                  response.data[0].Value != "CONVERTED_TO_CONTRACT" &&
                  response.data[0].Value != "CONVERTED_TO_DEAL"
                ) {
                  vm.FetchData();
                  box = vm.ShowAlert(
                    vm.$t("LeadReopenedSuccessfully"),
                    "success",
                    true,
                    vm.$t("Alert")
                  );
                } else {
                  box = vm.ShowAlert(
                    vm.$t("CantReopenDueToConverted"),
                    "warning",
                    true,
                    vm.$t("Alert")
                  );
                }
              } else {
                box = vm.ShowAlert(
                  vm.$t("ReopenLeadError"),
                  "failure",
                  true,
                  vm.$t("Alert")
                );
              }
              setTimeout(function () {
                box.modal("hide");
              }, 4000);
            });
          }
        );
      }
    },
    FetchData: function () {
      var vm = this;
      vm.isLoading=true;  
      CampaignDataService.FormLayoutView(vm.ApiURL1).then(function (response) {
        var fullLeftJSON = response.data.LEFTDATA;
        vm.isLoading=false;
        var stausName = $.grep(fullLeftJSON, function (element, index) {
                                  return element.name == 'status_id';
                              });
                              if (stausName != null && stausName.length > 0) {
                                vm.stausName = stausName[0];
                              }
        vm.timelinedata = [];
        fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
          if (element.name == "role_title_id") {
            vm.dataToConvertLead.roletitle =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "home_phone") {
            vm.dataToConvertLead.home_phone = element.value;
          }
          if (element.name == "business_phone") {
            vm.dataToConvertLead.business_phone = element.value;
          }
          if (element.name == "contact_number") {
            vm.dataToConvertLead.contact_number = element.value;
          }
          if (element.name == "account_id") {
            vm.dataToConvertLead.account_id = element.value;
          }
          if (element.name == "channel_id") {
            vm.dataToConvertLead.lead_source_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "industry_type_id") {
            vm.dataToConvertLead.industry_type_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "lead_country") {
            vm.dataToConvertLead.lead_country =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "lead_state") {
            vm.dataToConvertLead.lead_state =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "external_source_id") {
            vm.dataToConvertLead.referred_by =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          } else if (element.name == "status_id") {
            vm.LeadStatus = vm.FilterDDLandNormalValue(element.value);
          } else if (element.name == "client_first_name") {
            vm.clienFirstName = element.value != null ? element.value : "";
          } else if (element.name == "client_last_name") {
            vm.clientLastName = element.value != null ? element.value : "";
          } else if (element.name == "lead_address1") {
            vm.clientaddr1 = element.value != null ? element.value : "";
          } else if (element.name == "lead_address2") {
            vm.clientaddr2 = element.value != null ? element.value : "";
          } else if (element.name == "lead_city") {
            vm.clientcity = element.value != null ? element.value : "";
          } else if (element.name == "lead_state") {
            vm.clientstate =
              element.value != null ? JSON.parse(element.value)[0].name : "";
          } else if (element.name == "lead_zipcode") {
            vm.clientzip = element.value != null ? element.value : "";
          } else if (element.name == "lead_company_name") {
            vm.clientcompany = element.value != null ? element.value : "";
          } else if (element.name == "client_email") {
            vm.defaultemailid = element.value != null ? element.value : "";
          } else if (element.name == "expected_close_date") {
            vm.dataToConvertLead.closing_date = element.value;
            vm.expirydate = element.value;
          } else if (element.name == "money_value") {
            vm.dataToConvertLead.ammount = element.value;
          } else if (element.name == "campaign_id") {
            vm.dataToConvertLead.campaign_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          } else if (element.name == "lead_owner_id") {
            vm.dataToConvertLead.lead_owner = element.value;
          }
          if (
            element.name == "commission_type" &&
            $.trim(element.value) != ""
          ) {
            element.form_field_visibility = "YES";
            element.display_order = 20;
          }
          if (element.name == "agreement_type" && $.trim(element.value) != "") {
            element.form_field_visibility = "YES";
            element.display_order = 19;
          }
          return element.form_field_visibility == "YES";
        });
        if (vm.leadName == "--") {
          vm.leadName = vm.clienFirstName + " " + vm.clientLastName;
        }
        if (vm.Acctab == "--") {
          vm.Acctab =
            vm.clienFirstName +
            " " +
            vm.clientLastName +
            " " +
            vm.clientcompany +
            " " +
            vm.clientaddr1 +
            " " +
            vm.clientaddr2 +
            " " +
            vm.clientcity +
            " " +
            (vm.clientstate == "Select" ? " " : vm.clientstate) +
            " " +
            vm.clientzip;
        }
        vm.dataToConvertLead.lead_name = vm.leadName;
        if (
          response.data.RIGHTDATA != null &&
          response.data.RIGHTDATA.length > 0
        ) {
          var fullJSON = response.data.RIGHTDATA;
          var gruopBy = "";
          $.each(
            fullJSON,
            $.proxy(function (index, item) {
              gruopBy =vm.$options.filters.formatDate(item.Complete_datetime);
              var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                return element.groupKey == gruopBy;
              });
              if (chkGroup.length == 0) {
                var returnedData = $.grep(fullJSON, function (element, index) {
                  return vm.$options.filters.formatDate(element.Complete_datetime)== gruopBy;
                });
                if (returnedData.length > 0) {
                  var objGroup = {
                    groupKey: gruopBy,
                    groupData: returnedData,
                  };
                  vm.timelinedata.push(objGroup);
                }
              }
            }, this)
          );
            
          vm.noRecordForTab = false;
        } else {
          vm.isLoading=false;  
          vm.noRecordForTab = true;
        }
        if (fullLeftJSON != null && fullLeftJSON.length > 0) {
          var groupId = fullLeftJSON[0].group_id;
          $.each(
            fullLeftJSON,
            $.proxy(function (index, item) {
              var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                return element.group_id == groupId;
              });
              if (item.name == 'campaign_name') {
                 vm.campaignName = item.value;
                                      }
              if (chkGroup.length == 0) {
                var returnedData = $.grep(
                  fullLeftJSON,
                  function (element, index) {
                    return element.group_id == groupId;
                  }
                );
                returnedData = returnedData.sort(function (a, b) {
                  var a1 = a.display_order,
                    b1 = b.display_order;
                  if (a1 == b1) return 0;
                  return a1 > b1 ? 1 : -1;
                });
                if (returnedData.length > 0) {
                  var objGroup = {
                    group_id: groupId,
                    group_name: returnedData[0].group_name,
                    LeftViewData: returnedData,
                  };
                  vm.LeftViewData.push(objGroup);
                }
              }
              groupId = item.group_id;
            }, this)
          );
        }
        vm.additionalOwnerPermission = response.data.PERMISSION_DATA;
        setTimeout(function () {
          vm.CheckBoxBootstrap();
        }, 500);
        setTimeout(function () {
          vm.SetResponsiveTab();
        }, 200);
        setTimeout(function () {
          vm.RadioButtonBootstrap();
        }, 500);
        vm.isDetailPage = true;
      });
    },
    changeDateForAddNoteUsrEnd: function (value) {
      try {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
        var getDate = new Date(
          moment(String(value)).year(),
          moment(String(value)).month(),
          moment(String(value)).date(),
          moment(String(value)).hours(),
          moment(String(value)).minutes()
        );
        if (userTimeZone[0].indexOf("+") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() - parseInt(userTimeZone[0].replace("+", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() - parseInt(userTimeZone[1]))
          );
        } else if (userTimeZone[0].indexOf("-") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() + parseInt(userTimeZone[0].replace("-", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() + parseInt(userTimeZone[1]))
          );
        }
        return moment(getDate, "YYYY-MM-DD HH:mm").format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
          " " +
          String(timeFormat)
        );
      } catch (e) {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
        return moment(String(value)).format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
          " " +
          String(timeFormat)
        );
      }
    },
    DeleteActivityData: function (id) {
