        );
      } else {
        this.showAddEventModal = this.showAddEventModal ? false : true;
      }
    },
    openCallEvent: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showCallModal = this.showCallModal ? false : true;
      }
    },

    openEmailModal() {
      this.sendEmailScheduleModal =
        this.sendEmailScheduleModal ? false : true;
    },
    openActivityModal: function (item) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("UpdateAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        if (item.activity_type == "Task") {
          this.showAddTaskModal = true;
          this.modalTaskItem = item;
        } else if (item.activity_type == "FollowUp") {
          this.showAddEventModal = true;
          this.modalFollowUpItem = item;
        } else if (item.activity_type == "Call") {
          this.showCallModal = true;
          this.modalCallItem = item;
        }
      }
    },
    DeleteActivity: function (item) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("DeleteAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.modalDeleteItem = item;
        this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
      }
    },
    openCRMAttachmentModal: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAttachment")
      ) {

        alertR(
          Globalize.localize("additionalOwnerAuthorityWarning"),
          true,
          Globalize.localize("Alert")
        );
      } else {
        this.showAttachmentModal =
          this.showAttachmentModal ? false : true;
      }
    },
    openAddGroupModel: function () {
      this.showAddGroupModal = this.showAddGroupModal ? false : true;
    },
    loadData: function (tabName, callFrom) {
      var vm = this;
      vm.DisableButton();
      vm.isDetailPage = false;

      if (tabName == "notes") {
        vm.isNotes = true;
        vm.IsDetails = false;
        vm.activeKey = 2;
        vm.CampaignStatistics = false;
        if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
      } else if (tabName == "timeline") {
        vm.activeKey = 1;
        vm.isDetailPage = true;
        vm.CampaignStatistics = false;
      } else if (tabName == "attachments") {
        vm.IsDetails = false;
        vm.isAttachment = true;
        vm.activeKey = 3;
        vm.CampaignStatistics = false;
      } else if (tabName == "activities") {
        vm.IsDetails = false;
        vm.activeKey = 4;
        vm.isActivity = true;
        vm.CampaignStatistics = false;
      } else if (tabName == "contacts") {
        vm.IsDetails = false;
        vm.activeKey = 6;
        vm.isContact = true;
        vm.CampaignStatistics = false;
      } else if (tabName == "schedule") {
        vm.IsDetails = false;
        vm.activeKey = 5;
        vm.IsSchedule = true;
        vm.CampaignStatistics = false;
      } else if (tabName == "leads") {
        vm.activeKey = 8;
        vm.IsLead = true;
        vm.IsDetails = false;
        vm.CampaignStatistics = false;
      } else if (tabName == "contactGroups") {
        vm.activeKey = 7;
        vm.IsDetails = false;
        vm.IsContactGroup = true;
        vm.CampaignStatistics = false;
      } else if (tabName == "deals") {
        vm.activeKey = 9;
        vm.IsDeals = true;
        vm.IsDetails = false;
        vm.CampaignStatistics = false;
      }else if (tabName == "CampaignStatistics") {
        vm.activeKey = 10;
        vm.CampaignStatistics = true;
        vm.IsDetails = false;
      }
      if (tabName != "" && tabName != null) {
        if (tabName == "attachments") {
          CampaignDataService.AttachmentListing(vm.ApiURL1).then(function (
            response
          ) {
            if (response.data != null) {
              vm.attachmentData = response.data.data;
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "activities") {
          CampaignDataService.ActivityListing(vm.ApiURL1).then(function (
            response
          ) {
            if (
              response.data != "" &&
              response.data != null &&
              response.data.data.length > 0
            ) {
              vm.activitiesData = response.data.data;

              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        
        } else if (tabName == "timeline") {
          CampaignDataService.TimelineListing(vm.ApiURL1).then(function (
            response
          ) {
            if (response.data != null) {
              vm.timelinedata = [];
              var fullJSON = response.data.data;
              var gruopBy = "";
              $.each(
                fullJSON,
                $.proxy(function (index, item) {
                  gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                  var chkGroup = $.grep(
                    vm.timelinedata,
                    function (element, index) {
                      return element.groupKey == gruopBy;
                    }
                  );
                  if (chkGroup.length == 0) {
                    var returnedData = $.grep(
                      fullJSON,
                      function (element, index) {
                        return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                      }
                    );
                    if (returnedData.length > 0) {
                      var objGroup = {
                        groupKey: gruopBy,
                        groupData: returnedData,
                      };
                      vm.timelinedata.push(objGroup);
                    }
                  }
                }, this)
              );

              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "contacts") {
          var url = `id=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
          CampaignDataService.ContactListing(url).then(function (response) {
            if (response.data != null) {
              vm.contactsData = response.data.data;
              vm.contactsHeader = response.data.schema;
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
              vm.contactsData = null;
            }
          });
        } else if (tabName == "email") {
          var url =
            vm.ApiURLEmail +
            vm.SortBy +
            "&sortExp=" +
            vm.SortExp +
            "&pageSize=" +
            vm.PageSize +
            "&pageNumber=" +
            vm.PageNumber;
          CampaignDataService.EmailListing(url).then(function (response) {
            if (response.data != null) {
              vm.EmailData = response.data.data;
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "notes") {
          vm.noteData = [];
          CampaignDataService.NoteListing(vm.ApiURL1).then(function (response) {
            if (response.data != null && response.data.data.length > 0) {
              vm.noteData = response.data.data;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 500);
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () {
                vm.setHeaderForSelectedTab();
              }, 500);
              setTimeout(function () {
                vm.bindLeftTabEventForSetHeading();
              }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "contactGroups") {
          var url = `id=${vm.$route.params.id}&moduleName=${vm.$route.params.moduleName}&subModuleCode=${vm.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`;
          CampaignDataService.ContactGroupListing(url).then(function (
            response
          ) {
            if (response.data.data.length > 0) {
              vm.contactsGroupData = response.data.data;
              setTimeout(function () {
                vm.CheckBoxBootstrap();
              }, 200);
              vm.noRecordForTab = false;
              $("[id^='chkAll']").prop("checked", false).removeAttr("checked");
              $(".chkItems:checkbox:checked")
                .prop("checked", false)
                .removeAttr("checked");
              setTimeout(function () {
                vm.CheckBoxBootstrap("table-contacts-group-content");
              }, 200);
            } else {
              vm.noRecordForTab = true;
              vm.contactsData = null;
              $("[id^='chkAll']").prop("checked", false).removeAttr("checked");
              $(".chkItems:checkbox:checked")
                .prop("checked", false)
                .removeAttr("checked");
            }
          });
        } else if (tabName == "leads") {
          var url = `id=${vm.$route.params.id}&moduleName=${vm.$route.params.moduleName}&subModuleCode=${vm.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`;

          CampaignDataService.GetLeadListing(url).then(function (response) {
            if (response.data != null) {
              if (response.data.data.length > 0) {
                vm.leadsData = response.data.data;
                vm.leadsHeader = response.data.schema;
                var newheader = [
                  {
                    COLUMN_NAME: "lead_date",
                    DATA_TYPE: "date",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: null,
                  },
                  {
                    COLUMN_NAME: "lead_owner_id",
                    DATA_TYPE: "select",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "LeadOwner",
                  },
                  {
                    COLUMN_NAME: "client_first_name",
                    DATA_TYPE: "string",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "FirstName",
                  },
                  {
                    COLUMN_NAME: "client_last_name",
                    DATA_TYPE: "string",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "LastName",
                  },
                  {
                    COLUMN_NAME: "client_email",
                    DATA_TYPE: "string",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: null,
                  },
                  {
                    COLUMN_NAME: "contact_number",
                    DATA_TYPE: "string",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Mobile",
                  },
                  {
                    COLUMN_NAME: "campaign_id",
                    DATA_TYPE: "select",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "CampaignName",
                  },
                  {
                    COLUMN_NAME: "channel_id",
                    DATA_TYPE: "select",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Lead Source",
                  },
                  {
                    COLUMN_NAME: "industry_type_id",
                    DATA_TYPE: "select",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: null,
                  },
                  {
                    COLUMN_NAME: "status_id",
                    DATA_TYPE: "select",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                  },
                  {
                    COLUMN_NAME: "expected_close_date",
                    DATA_TYPE: "date",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: null,
                  },
                  {
                    COLUMN_NAME: "modified_at",
                    DATA_TYPE: "date",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "LastModifyDate",
                  },
                  {
                    COLUMN_NAME: "money_value",
                    DATA_TYPE: "decimal",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: null,
                  },
                ];
                vm.leadsHeader = newheader;
                setTimeout(function () {
                  vm.ResponsiveDataTable("table-lead-content");
                }, 500);
                vm.noRecordForTab = false;
              } else {
                vm.noRecordForTab = true;
              }
            }
          });
        } else if (tabName == "deals") {
          var url = `id=${vm.$route.params.id}&moduleName=${vm.$route.params.moduleName}&subModuleCode=${vm.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}`;
          CampaignDataService.GetDealListing(url).then(function (response) {
            if (response.data != null && response.data.data.length > 0) {
              vm.dealsData = response.data.data;
              vm.dealsHeader = response.data.schema;
              setTimeout(function () {
                vm.ResponsiveDataTable("table-deal-content");
              }, 500);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "schedule") {
          var url =
            "?id=0&refId=" +
            vm.$route.params.id +
            "&moduleName=" +
            vm.$route.params.moduleName +
            "&subModuleCode=" +
            vm.$route.params.subModuleCode +
            "&sortBy=" +
            vm.SortBy +
            "&sortExp=" +
            vm.SortExp +
            "&pageSize=" +
            vm.PageSize +
            "&pageNum=" +
            vm.PageNumber;
        
          CampaignDataService.ScheduleListing(url).then(function (response) {
            if (response.data != null) {
              vm.EmailScheduleData = response.data.data;
              setTimeout(function () {
                vm.ResponsiveDataTable("table-schedule-content");
              }, 500);
              vm.noRecordForTab = false;
            } else {
              vm.noRecordForTab = true;
            }
          });
        }
      }
    },
    ChangeCRMLeadOwnerCheck: function (event) {
      var varObj = this;
      $(".chkItems:checkbox:checked")
        .not("[id^='chkAll']")
        .each(function () {
          varObj.OwnerIds.push($(this).val());
        });

      if (this.OwnerIds.length > 0) {
        this.showLeadOwnerModal =
          this.showLeadOwnerModal ? false : true;
      } else {
        alert(varObj.$t("Selectchangeleadowner"));
      }
    },
    openCRMEmailModal: function (item) {
      var type = "AddEmail";
      if (item != null) {
        type = "UpdateEmail";
      }
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission(type)
      ) {
        alertR(
          Globalize.localize("additionalOwnerAuthorityWarning"),
          true,
          Globalize.localize("Alert")
        );
      } else {
        this.sendEmailModal = this.sendEmailModal ? false : true;
        editid = item;
      }
    },
    openCRMProductModel: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddProducts")
      ) {
        alertR(
          Globalize.localize("additionalOwnerAuthorityWarning"),
          true,
          Globalize.localize("Alert")
        );
      } else {
        this.showProductModal = this.showProductModal ? false : true;
      }
    },
    GetEmailInfo: function (item) {
      this.EmailInfo = item;
      this.sendEmailModal = this.sendEmailModal ? false : true;
    },
    openCRMExistingContactModel: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddContact")
      ) {
        alertR(
          Globalize.localize("additionalOwnerAuthorityWarning"),
          true,
          Globalize.localize("Alert")
        );
      } else {
        this.showExistingContactModal =
          this.showExistingContactModal ? false : true;
      }
    },
    SendBulkEmail: function (event) {
      var vm = this;
      var ids = "";
      var val = "";
      if (!$("#tab-contacts-group").hasClass("active")) {
        vm.contactsGroupData = null;
        $("#tab-contacts-group").trigger("click");
        vm.loadData("contactGroups");
        ShowAlert(
          vm.$t("Please select atleast one group."),
          "warning",
          true,
          vm.$t("Alert")
        );
        return;
      }
      $("#tab-contacts-group-content .chkItems:checkbox:checked")
        .not("[id^='chkAll']")
        .not(".CloseLeadInput")
        .each(function () {
          if (val.length > 0) val += ",";
          val += $(this).val();
          ids = val;
        });
      if (ids == '') {
        vm.ShowAlert("Please select atleast one group.", "warning", true, this.$t('Alert'));
        return false;
      }

      
      vm.showAddEmailTemplate = true;
      vm.modalItem = ids;
    },
    openCRMContactModal: function (contactDetails) {
      var type = "AddContact";
      if (contactDetails != null) {
        type = "UpdateContact";
      }
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission(type)
      ) {
        alertR(
          Globalize.localize("additionalOwnerAuthorityWarning"),
          true,
          Globalize.localize("Alert")
        );
      } else {
        this.iscontactDetails = contactDetails;
        this.showContactModal = this.showContactModal ? false : true;
      }
    },
  },
};
</script>
    